import { useEffect, useState } from "react";
import { List, Row, Col, Input, Button, Modal } from "antd";
import MedicalHistory from "../../../shared/components/MedicalHistory";
import patientsServices from "../../../shared/services/patients.services";
import { IPatient } from "../../../shared/interfaces/patients.interfaces";
import moment from "moment";
import { GenderType } from "../../../shared/enum/gender.enum";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import FormCreatePatient from "./FormCreatePatient";
import { Auth } from "../../../shared/utils/auth";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";

const { Search } = Input;

let statePatientsListStatic: IPatient[] = [];

const Family = () => {
  const tokenDecode = Auth.tokenDecode();

  const [statePatientsList, setStatePatientsList] = useState<IPatient[]>([]);
  const [statePatientSelected, setStatePatientSelected] = useState<IPatient>();

  useEffect(() => {
    if (tokenDecode.id) {
      patientsServices
        .getListFamily(tokenDecode.id)
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            setStatePatientsList(data);
            statePatientsListStatic = data;
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
    }
  }, [tokenDecode.id]);

  const onSearch = (value: string) => {
    setStatePatientsList(
      statePatientsListStatic.filter(
        (patient) =>
          patient.firstName.toLowerCase().includes(value.toLowerCase()) ||
          patient.lastName.toLowerCase().includes(value.toLowerCase()) ||
          patient.email.toLowerCase().includes(value.toLowerCase()) ||
          patient.dni?.toLowerCase().includes(value.toLowerCase())
      )
    );
  };

  const [modalCrear, setModalCrear] = useState<IModal>({
    visible: false,
  });

  return (
    <>
      <Button type="primary" onClick={() => setModalCrear({ ...modalCrear, visible: true })}>
        Agregar hijos
      </Button>
      <div style={{ height: 10 }}></div>
      <Row gutter={[10, 10]}>
        <Col
          lg={10}
          style={{
            backgroundColor: "#FFFFFF",
            height: "85vh",
            overflow: "scroll",
          }}
        >
          <Search
            placeholder=""
            // onSearch={onSearch}
            // enterButton
            onChange={(e) => {
              onSearch(e.target.value);
            }}
            style={{
              marginTop: 5,
            }}
          />
          <br />
          <br />
          <List
            itemLayout="horizontal"
            dataSource={statePatientsList}
            renderItem={(patient) => {
              const age = moment().diff(patient.birthDate, "years");
              const gender = patient.gender === GenderType.FELAME ? "Femenino" : "Masculino";
              return (
                <List.Item
                  style={{
                    backgroundColor: patient.id === statePatientSelected?.id ? "#EDEBE8" : "",
                    paddingLeft: 5,
                    cursor: "pointer",
                  }}
                  onClick={() => {
                    setStatePatientSelected(patient);
                  }}
                >
                  <List.Item.Meta
                    // avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />}
                    title={`${patient.firstName} ${patient.lastName}`}
                    description={`${age} años. ${gender}`}
                  />
                </List.Item>
              );
            }}
          />
        </Col>
        <Col
          lg={14}
          style={{
            backgroundColor: "#FFFFFF",
            height: "85vh",
            overflow: "scroll",
          }}
        >
          <MedicalHistory patientSelected={statePatientSelected} />
        </Col>
      </Row>

      <FormCreatePatient
        modalCrear={modalCrear}
        setModalCrear={setModalCrear}
        setStatePatientsList={setStatePatientsList}
        statePatientsListStatic={statePatientsListStatic}
      />
    </>
  );
};

export default Family;
