import { Button, Col, Form, Input, Modal, Row } from "antd";
import moment from "moment";
import { LABEL_COL_AND_WRAPPER_COL } from "../../../shared/utils/formats";
import consultationsServices from "../../../shared/services/consultations.services";
import { IConsultation } from "../../../shared/interfaces/consultations.interfaces";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";
import FormItemDateHour from "../../../shared/components/Consultation/FormItemDateHour";
import { Auth } from "../../../shared/utils/auth";
import { RoleType } from "../../../shared/enum/roletype.enum";

type TypeModalReschedule = {
  consultation: IConsultation;
  modalReschedule: IModal;
  setModalReschedule: any;
  setStateListConsultations: any;
  setStateSelected?: any;
};

type TypeFormReschedule = {
  date: Date;
  hour: Date;
  observations: string;
};

const ModalReschedule = (props: TypeModalReschedule) => {
  const tokenDecode = Auth.tokenDecode();
  const [form] = Form.useForm();
  const { consultation, modalReschedule, setModalReschedule, setStateListConsultations, setStateSelected } = props;
  const onFinishReschedule = (values: TypeFormReschedule) => {
    const date = moment(values.date).format("YYYY-MM-DD") + " " + moment(values.hour).format("HH:mm");

    consultationsServices
      .rescheduleConsultation(consultation.id, {
        date: moment(date).utc(true).toDate(),
        observations: [`${values.observations}`],
      })
      .then((response: TypeResponse) => {
        const { statusCode, data } = response;
        if (statusCode === 200) {
          Modal.success({
            title: "Hola",
            content: "Consulta reagendada",
          });
          setModalReschedule({ visible: false });
          setStateListConsultations((consultations: IConsultation[]) => {
            const updateConsultations = consultations.map((consultation) => {
              if (consultation.id === data.id) {
                consultation.date = data.date;
                consultation.observations = data.observations;
              }
              return consultation;
            });

            return updateConsultations;
          });
        }
      })
      .catch((errors: TypeResponseError) => {
        const { error, message, statusCode } = errors;
        Modal.error({
          title: "Hola",
          content: `${message} (${statusCode} ${error})`,
        });
      });
  };

  let doctorId = "";
  if (tokenDecode.id && tokenDecode.role === RoleType.DOCTOR) {
    doctorId = tokenDecode.id;
  } else if (tokenDecode.id && tokenDecode.role === RoleType.PATIENT && consultation.doctor?.id) {
    doctorId = consultation.doctor.id;
  }

  return (
    <Modal
      title={<span>Reagendar consulta</span>}
      visible={modalReschedule.visible}
      onCancel={() => {
        setModalReschedule({ visible: false });
        if (setStateSelected) setStateSelected(null);
      }}
      centered
      footer={false}
    >
      <Form
        form={form}
        name={consultation.id}
        onFinish={onFinishReschedule}
        {...LABEL_COL_AND_WRAPPER_COL}
        initialValues={{
          observations: consultation.observations[0],
          date: moment(consultation.date).utc(false),
          hour: moment(consultation.date).utc(false),
        }}
      >
        <Row gutter={10}>
          <FormItemDateHour doctorId={doctorId} formCreateConsultation={form} consultationType={consultation.type} />

          <Col lg={24}>
            <Form.Item
              name="observations"
              label={<strong>Observaciones</strong>}
              rules={[{ required: true, message: "Colocá alguna observación para saber el motivo de la consulta" }]}
            >
              <Input.TextArea />
            </Form.Item>
          </Col>
        </Row>

        <br />
        <div style={{ textAlign: "center" }}>
          <Button type="primary" htmlType="submit">
            Reagendar consulta
          </Button>
        </div>
      </Form>
    </Modal>
  );
};

export default ModalReschedule;
