import { useHistory } from "react-router";
import { List, Row, Col, Input, Typography, Space, Tag } from "antd";
import { AlertOutlined } from "@ant-design/icons";
import moment from "moment";
import { getConsultationsTypeTitle } from "../../utils/consultations";
import { GenderType } from "../../enum/gender.enum";
import { FORMAT_TIME_h_mm_A } from "../../utils/formats";
import ButtonReschedule from "../ButtonReschedule";
import ButtonCancel from "../ButtonCancel";
import { ConsultationsStatus } from "../../enum/consultations-status.enum";
import { ConsultationsType } from "../../enum/consultations-type.enum";
import { IConsultation } from "../../interfaces/consultations.interfaces";
import ModalReschedule from "../../../apps/doctor/Calendar/ModalReschedule";
import ModalCancel from "../../../apps/doctor/Calendar/ModalCancel";
import { useState } from "react";
import { IModal } from "../../interfaces/modals.interfaces";
import ConsultationStatusTag from "../ConsultationStatusTags";
import { Auth } from "../../utils/auth";
import { RoleType } from "../../enum/roletype.enum";

type TypeConsultationList = {
  consultations: IConsultation[];
  setStateList: any;
  stateListStatic: IConsultation[];
};

const ConsultationList = (props: TypeConsultationList) => {
  const tokenDecode = Auth.tokenDecode();
  const history = useHistory();
  const { consultations, setStateList, stateListStatic } = props;

  const [stateSelected, setStateSelected] = useState<IConsultation>();
  const [modalReschedule, setModalReschedule] = useState<IModal>({ visible: false });
  const [modalCancel, setModalCancel] = useState<IModal>({ visible: false });

  const onSearch = (value: string) => {
    setStateList(
      stateListStatic.filter(
        (consultation) =>
          consultation.id.toLowerCase().includes(value.toLowerCase()) ||
          consultation.type.toLowerCase().includes(value.toLowerCase()) || // TODO: colocar que lea por tipo, convertir ConsultationTypes
          consultation.patient.firstName.toLowerCase().includes(value.toLowerCase()) ||
          consultation.patient.lastName.toLowerCase().includes(value.toLowerCase()) ||
          consultation.patient.dni?.toLowerCase().includes(value.toLowerCase()) ||
          consultation.patient.email?.toLowerCase().includes(value.toLowerCase()) ||
          consultation.doctor?.firstName.toLowerCase().includes(value.toLowerCase()) ||
          consultation.doctor?.lastName.toLowerCase().includes(value.toLowerCase())
      )
    );
  };

  return (
    <>
      <Row>
        <Col
          lg={24}
          style={{
            backgroundColor: "#FFFFFF",
            height: "85vh",
            overflow: "scroll",
          }}
        >
          <Input.Search
            placeholder=""
            onChange={(e) => {
              onSearch(e.target.value);
            }}
          />
          Total: {consultations.length}
          <List
            itemLayout="horizontal"
            dataSource={consultations}
            renderItem={(consultation) => {
              let minutes = moment(consultation.date).diff(moment().utcOffset(0, true), "minutes");
              const hours = moment(consultation.date).diff(moment().utcOffset(0, true), "hours");
              const dateHour = moment(consultation.date).utc(false).format(FORMAT_TIME_h_mm_A);
              const eseMinutes = minutes > 1 || minutes < -1 ? "s" : "";
              const eseOurs = hours > 1 || hours < -1 ? "s" : "";
              let timeDiff: string | JSX.Element = `en ${
                minutes < 60 ? `${minutes} minuto${eseMinutes}` : `${hours} hora${eseOurs}`
              }  `;

              if (minutes < 1) {
                minutes = Number(minutes.toString().replace("-", ""));
                timeDiff = `hace ${
                  minutes < 60
                    ? `${minutes} minuto${eseMinutes}`
                    : `${hours.toString().replace("-", "")} hora${eseOurs}`
                }  `;
              }

              if (minutes === 0) {
                timeDiff = (
                  <>
                    <Tag icon={<AlertOutlined />} color="success">
                      ahora
                    </Tag>
                  </>
                );
              }

              let observations: string = consultation.observations[0];
              observations = `${observations.slice(0, 50)} ${observations.length > 50 ? "..." : ""}`;

              return (
                <List.Item
                  style={{
                    backgroundColor: consultation.id === stateSelected?.id ? "#EDEBE8" : "",
                    paddingLeft: 5,
                    cursor: "pointer",
                    alignItems: "start",
                  }}
                  onClick={() => {
                    setStateSelected(consultation);
                  }}
                >
                  <div
                    style={{ width: "100%", display: "flex" }}
                    onClick={() => {
                      if (tokenDecode.role === RoleType.DOCTOR) {
                        if (consultation.status === ConsultationsStatus.PENDING) {
                          history.push({
                            pathname: "/doctor/attending",
                            state: { consultation },
                          });
                        } else {
                          history.push({
                            pathname: "/doctor/attended",
                            state: { consultation },
                          });
                        }
                      } else if (tokenDecode.role === RoleType.PATIENT) {
                        history.push({
                          pathname: "/patient/attended",
                          state: { consultation },
                        });
                      }
                    }}
                  >
                    <div style={{ width: 240 }}>
                      <div>
                        {consultation.status === ConsultationsStatus.PENDING ? (
                          <Typography.Text strong>
                            <Tag color="default">{getConsultationsTypeTitle(consultation.type)}</Tag>
                          </Typography.Text>
                        ) : (
                          <Typography.Text>
                            <Tag color="default">{getConsultationsTypeTitle(consultation.type)}</Tag>
                          </Typography.Text>
                        )}
                        &nbsp;
                      </div>
                      {tokenDecode.role === RoleType.DOCTOR && (
                        <div>
                          {consultation.status === ConsultationsStatus.PENDING ? (
                            <Typography.Text strong>
                              {consultation.patient.firstName} {consultation.patient.lastName}.{" "}
                              {consultation.patient.gender === GenderType.FELAME ? "F" : "M"} de{" "}
                              {moment().diff(consultation.patient.birthDate, "years")} años.
                            </Typography.Text>
                          ) : (
                            <Typography.Text>
                              {consultation.patient.firstName} {consultation.patient.lastName}.{" "}
                              {consultation.patient.gender === GenderType.FELAME ? "F" : "M"} de{" "}
                              {moment().diff(consultation.patient.birthDate, "years")} años.
                            </Typography.Text>
                          )}
                        </div>
                      )}
                      {tokenDecode.role === RoleType.PATIENT && consultation.doctor && (
                        <div>
                          {consultation.status === ConsultationsStatus.PENDING ? (
                            <>
                              <div>
                                <Typography.Text strong>
                                  {consultation.doctor.gender === GenderType.FELAME ? "Dra." : "Dr."}{" "}
                                  {consultation.doctor.firstName} {consultation.doctor.lastName}
                                </Typography.Text>
                              </div>
                              <div>
                                {consultation.patient.familyId &&
                                  `${consultation.patient.firstName} ${consultation.patient.lastName}`}
                              </div>
                            </>
                          ) : (
                            <>
                              <div>
                                <Typography.Text>
                                  {consultation.doctor.gender === GenderType.FELAME ? "Dra." : "Dr."}{" "}
                                  {consultation.doctor.firstName} {consultation.doctor.lastName}
                                </Typography.Text>
                              </div>
                              <div>
                                {consultation.patient.familyId &&
                                  `${consultation.patient.firstName} ${consultation.patient.lastName}`}
                              </div>
                            </>
                          )}
                        </div>
                      )}
                      <div style={{ height: 5 }}></div>
                    </div>
                    <div>
                      <ConsultationStatusTag status={consultation.status} />

                      <span className="list-item-time-diff">{timeDiff}</span>
                      <br />
                      {consultation.status === ConsultationsStatus.PENDING ? (
                        <Typography.Text strong>{observations}</Typography.Text>
                      ) : (
                        <Typography.Text type="secondary">{observations}</Typography.Text>
                      )}
                    </div>
                  </div>

                  <div style={{ width: 100, textAlign: "right", alignItems: "start" }}>
                    <div className="list-item-hour">
                      {consultation.status === ConsultationsStatus.PENDING ? (
                        <Typography.Text strong>{dateHour}</Typography.Text>
                      ) : (
                        <Typography.Text type="secondary">{dateHour}</Typography.Text>
                      )}
                    </div>

                    <div className="events-options">
                      <Space>
                        {(consultation.type === ConsultationsType.FACE_TO_FACE ||
                          consultation.type === ConsultationsType.SURGERY ||
                          consultation.type === ConsultationsType.ONLINE) && (
                          <>
                            {consultation.status === ConsultationsStatus.PENDING && (
                              <ButtonReschedule
                                onClick={() => {
                                  setModalReschedule({ visible: true });
                                }}
                              />
                            )}
                          </>
                        )}

                        {consultation.status === ConsultationsStatus.PENDING && (
                          <ButtonCancel
                            onClick={() => {
                              setModalCancel({ visible: true });
                            }}
                          />
                        )}
                      </Space>
                    </div>
                  </div>
                </List.Item>
              );
            }}
          />
        </Col>
      </Row>
      {stateSelected && (
        <>
          <ModalReschedule
            consultation={stateSelected}
            modalReschedule={modalReschedule}
            setModalReschedule={setModalReschedule}
            setStateListConsultations={setStateList}
            setStateSelected={setStateSelected}
          />
          <ModalCancel
            consultation={stateSelected}
            modalCancel={modalCancel}
            setModalCancel={setModalCancel}
            setStateListConsultations={setStateList}
            setStateSelected={setStateSelected}
          />
        </>
      )}
    </>
  );
};

export default ConsultationList;
