import { useEffect, useState } from "react";
import { IPatient } from "../../../shared/interfaces/patients.interfaces";
import { Auth } from "../../../shared/utils/auth";
import MedicalHistory from "../../../shared/components/MedicalHistory";

const MedicalHistoryPreview = () => {
  const tokenDecode = Auth.tokenDecode();
  const [statePatientSelected, setStatePatientSelected] = useState<IPatient>();

  useEffect(() => {
    if (
      tokenDecode.id &&
      tokenDecode.firstName &&
      tokenDecode.lastName &&
      tokenDecode.email &&
      tokenDecode.status &&
      tokenDecode.gender &&
      tokenDecode.birthDate
    ) {
      setStatePatientSelected({
        id: tokenDecode.id,
        firstName: tokenDecode.firstName,
        lastName: tokenDecode.lastName,
        email: tokenDecode.email,
        dni: tokenDecode.dni || "",
        status: tokenDecode.status,
        gender: tokenDecode.gender,
        birthDate: tokenDecode.birthDate,
        familyId: "", //  no se le agrego porque los pacientes que pueden ingresar son mayores de edad y no tienen familiares representantes asociados
      });
    }
  }, [
    tokenDecode.birthDate,
    tokenDecode.dni,
    tokenDecode.email,
    tokenDecode.gender,
    tokenDecode.firstName,
    tokenDecode.id,
    tokenDecode.status,
    tokenDecode.lastName,
  ]);

  return <>{statePatientSelected && <MedicalHistory patientSelected={statePatientSelected} />}</>;
};

export default MedicalHistoryPreview;
