import { Alert, Button, Col, Form, Input, InputNumber, Modal, Row, Typography } from "antd";
import { errors_translate_es } from "../../../shared/errors/es";
import { IStock } from "../../../shared/interfaces/inventory.interfaces";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import inventoryServices from "../../../shared/services/inventory.services";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";
import { Auth } from "../../../shared/utils/auth";

interface IFormCreate {
  modalCrear: IModal;
  setModalCrear: any;
  setStateList: any;
  stateListStatic: IStock[];
}

const FormCreateStock = (props: IFormCreate) => {
  const tokenDecode = Auth.tokenDecode();

  const { modalCrear, setModalCrear, setStateList, stateListStatic } = props;
  const [formCreate] = Form.useForm();

  const onFinish = (values: IStock) => {
    if (tokenDecode.id) {
      inventoryServices
        .addInventory({
          doctorId: tokenDecode.id,
          sku: values.sku,
          name: values.name,
          stock: values.stock,
        })
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          if (statusCode === 201) {
            formCreate.resetFields();
            setModalCrear({ visible: false });

            setStateList((state: IStock[]) => [
              ...state,
              {
                id: data.id,
                doctorId: data.doctorId,
                sku: data.sku,
                name: data.name,
                stock: data.stock,
              },
            ]);

            stateListStatic.push({
              id: data.id,
              doctorId: data.doctorId,
              sku: data.sku,
              name: data.name,
              stock: data.stock,
            });
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;
          if (statusCode === 409) {
            Modal.warning({
              content: errors_translate_es[message],
            });
          } else {
            Modal.error({
              title: "Hola",
              content: `${message} (${statusCode} ${error})`,
            });
          }
        });
    }
  };

  return (
    <Modal
      title="Agregar al inventario"
      visible={modalCrear.visible}
      onCancel={() => setModalCrear({ ...modalCrear, visible: false })}
      footer={false}
    >
      <Form
        labelCol={{ span: 24 }}
        wrapperCol={{ span: 24 }}
        initialValues={{ remember: true, stock: 1 }}
        onFinish={onFinish}
        form={formCreate}
      >
        <Row gutter={[10, 10]}>
          <Col lg={6}>
            <Form.Item
              label={<strong>Código</strong>}
              name="sku"
              rules={[
                { required: true, message: "Escribe el código" },
                { min: 5, message: "Minimo 5 catacteres" },
              ]}
            >
              <Input />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item
              label={<strong>Nombre</strong>}
              name="name"
              rules={[
                { required: true, message: "Escribe el nombre del item" },
                { min: 5, message: "Minimo 5 catacteres" },
              ]}
            >
              <Input maxLength={50} />
            </Form.Item>
          </Col>
          <Col lg={6}>
            <Form.Item
              label={<strong>Stock</strong>}
              name="stock"
              rules={[
                { required: true, message: "Coloca la cantidad" },
                { type: "number", message: "Escribe un número" },
              ]}
            >
              <InputNumber style={{ width: "100%" }} min={1} max={10000000} maxLength={8} />
            </Form.Item>
          </Col>

          <Col lg={24} style={{ textAlign: "center" }}>
            <Button type="primary" htmlType="submit">
              Agregar al inventario
            </Button>
          </Col>

          <Col lg={24}>
            <br />
            <Alert
              message={
                <Typography.Text>
                  Stock máximo 10 millones, si necesitas mas, escribe a ayuda@tuhospitalvirtual.com
                </Typography.Text>
              }
              type="info"
            />
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

export default FormCreateStock;
