import { Switch, Route } from "react-router-dom";
import ResultNotFound from "../../shared/components/Results/ResultNotFound";
import Login from "./Login";
import NuevaClave from "./NuevaClave";
import OlvidoClave from "./OlvidoClave";

const Auth = () => {
  return (
    <Switch>
      <Route exact path="/auth/login" component={Login}></Route>
      <Route exact path="/auth/olvide-clave" component={OlvidoClave}></Route>
      <Route exact path="/auth/nueva-clave" component={NuevaClave}></Route>
      <Route path="/" component={ResultNotFound} />
    </Switch>
  );
};

export default Auth;
