import { Timeline, Typography } from "antd";
import ConsultationInfo from "../../../shared/components/ConsultationInfo";
import { ConsultationsStatus } from "../../../shared/enum/consultations-status.enum";
import { IConsultation, TypeEvaluation } from "../../../shared/interfaces/consultations.interfaces";

type TypePreview = {
  consultation: IConsultation;
};

const Preview = (props: TypePreview) => {
  const { consultation } = props;

  let evaluation: TypeEvaluation = {
    cie10: "",
    observations: "",
  };
  if (consultation.status === ConsultationsStatus.ATTENDED) {
    evaluation = {
      cie10: consultation.observations.length === 2 ? consultation.observations[0] : "",
      observations:
        consultation.observations.length === 2 ? consultation.observations[1] : consultation.observations[0],
    };
  }

  return (
    <Timeline>
      <br />
      <Timeline.Item>
        <ConsultationInfo consultation={consultation} />
      </Timeline.Item>

      {consultation.status === ConsultationsStatus.PENDING && (
        <Timeline.Item>
          <div>
            <Typography.Text strong>Evaluación</Typography.Text>
          </div>
          {consultation.evaluation && consultation.evaluation?.cie10 && (
            <>
              <div>
                &nbsp;&nbsp;<Typography.Text underline>CIE-10</Typography.Text>
              </div>
              <div style={{ paddingLeft: 15 }}>{consultation.evaluation.cie10}</div>
            </>
          )}
          {consultation.evaluation?.observations && consultation.evaluation?.observations.trim().length > 0 ? (
            <>
              <div>
                &nbsp;&nbsp;<Typography.Text underline>Observaciones</Typography.Text>
              </div>
              <div
                style={{ paddingLeft: 15 }}
                dangerouslySetInnerHTML={{
                  __html: consultation.evaluation.observations.replaceAll("\n", "<br />"),
                }}
              ></div>
            </>
          ) : (
            <span>No se indicaron </span>
          )}
        </Timeline.Item>
      )}

      {consultation.status === ConsultationsStatus.ATTENDED && (
        <Timeline.Item>
          <div>
            <Typography.Text strong>Evaluación</Typography.Text>
          </div>
          {evaluation.observations && evaluation.cie10 && (
            <>
              <div>
                &nbsp;&nbsp;<Typography.Text underline>CIE-10</Typography.Text>
              </div>
              <div style={{ paddingLeft: 15 }}>{evaluation.cie10}</div>
            </>
          )}
          {evaluation.observations && evaluation.observations.trim().length > 0 ? (
            <>
              <div>
                &nbsp;&nbsp;<Typography.Text underline>Observaciones</Typography.Text>
              </div>
              <div
                style={{ paddingLeft: 15 }}
                dangerouslySetInnerHTML={{
                  __html: evaluation.observations.replaceAll("\n", "<br />"),
                }}
              ></div>
            </>
          ) : (
            <span>No se indicaron </span>
          )}
        </Timeline.Item>
      )}

      <Timeline.Item>
        <div>
          <Typography.Text strong>Recetas</Typography.Text>
        </div>
        {consultation.prescriptions && consultation.prescriptions.length < 1 && <span>No se indicaron </span>}
        <ul>
          {consultation.prescriptions &&
            consultation.prescriptions.map((prescription, index) => <li key={index}>{prescription}</li>)}
        </ul>
      </Timeline.Item>
      <Timeline.Item>
        <div>
          <Typography.Text strong>Exámenes</Typography.Text>
        </div>
        {consultation.exams && consultation.exams.length < 1 && <span>No se indicaron </span>}
        <ul>{consultation.exams && consultation.exams.map((exam, index) => <li key={index}>{exam}</li>)}</ul>
      </Timeline.Item>
      <Timeline.Item>
        <div style={{ marginBottom: 6 }}>
          <Typography.Text strong>Cotización</Typography.Text>
        </div>
        {consultation.quote && consultation.quote.length < 1 && <span>No se indicaron </span>}
        {consultation.quote && consultation.quote.length > 0 && (
          <table>
            <thead>
              <tr style={{ border: "1px solid #8C8C8C" }}>
                <td style={{ padding: "0px 10px 0px 10px" }}>
                  <Typography.Text type="secondary" strong>
                    Servicio
                  </Typography.Text>
                </td>
                <td style={{ padding: "0px 10px 0px 10px" }}>
                  <Typography.Text type="secondary" strong>
                    Costo
                  </Typography.Text>
                </td>
              </tr>
            </thead>
            <tbody>
              {consultation.quote &&
                consultation.quote.map((quote, index) => (
                  <tr key={index} style={{ border: "1px solid #8C8C8C", paddingLeft: 5 }}>
                    <td style={{ padding: "0px 10px 0px 10px" }}>{quote.service}</td>
                    <td style={{ padding: "0px 10px 0px 10px" }}>{quote.cost}</td>
                  </tr>
                ))}
            </tbody>
          </table>
        )}
      </Timeline.Item>
    </Timeline>
  );
};

export default Preview;
