import { ConsultationsType } from "../enum/consultations-type.enum";

export const getConsultationsTypeTitle = function (consultationType: ConsultationsType): string {
  if (consultationType === ConsultationsType.FACE_TO_FACE) {
    return "Consulta Presencial";
  } else if (consultationType === ConsultationsType.SURGERY) {
    return "Cirugía";
  } else if (consultationType === ConsultationsType.ONLINE) {
    return "Consulta Online";
  } else if (consultationType === ConsultationsType.WRITTEN) {
    return "Consulta Escrita";
  } else {
    return "Not found ConsultationsType";
  }
};
