import requestApiConsultations from "../../config/requestApiConsultations";
import { IUser } from "../interfaces/user.interfaces";

interface ICredentials {
  username: string;
  password: string;
}

interface IRecoveryPassSendEmail {
  email: string;
}

interface IRecoveryPassByEmail {
  userId: string;
  email: string;
  tokenPasswordRecovery: string;
  password: string;
}

interface IAuthServices {
  signup(user: IUser): Promise<any>;
  login(credentials: ICredentials): Promise<any>;
  recoveryPasswordSendEmail(recovery: IRecoveryPassSendEmail): Promise<any>;
  recoveryPasswordByEmail(recovery: IRecoveryPassByEmail): Promise<any>;
}

const authServices: IAuthServices = {
  signup: function (user) {
    return requestApiConsultations({
      method: "POST",
      url: "/auth/signup",
      data: user,
    });
  },
  login: function (credentials) {
    return requestApiConsultations({
      method: "POST",
      url: "/auth/login",
      data: credentials,
    });
  },
  recoveryPasswordSendEmail: function (recovery) {
    return requestApiConsultations({
      method: "POST",
      url: "/auth/rpbe",
      data: recovery,
    });
  },
  recoveryPasswordByEmail: function (recovery) {
    return requestApiConsultations({
      method: "PUT",
      url: "/auth/rpbe",
      data: recovery,
    });
  },
};

export default authServices;
