import { Row, Col, Input, Button, Modal, Form, Select, DatePicker } from "antd";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import locale from "antd/es/date-picker/locale/es_ES";
import { panama } from "../../../shared/data/specialities";
import { IUser } from "../../../shared/interfaces/user.interfaces";
import { RoleType } from "../../../shared/enum/roletype.enum";
import { ICreateDoctor, IDoctorDetail } from "../../../shared/interfaces/doctor.interfaces";
import { FORMAT_DATE_DD_MM_YYY } from "../../../shared/utils/formats";
import { medicalCenters } from "../../../shared/data/medicalCenters";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";
import doctorServices from "../../../shared/services/doctor.services";
import { useEffect } from "react";
import moment, { Moment } from "moment";

interface IFormUpdateDoctor {
  modalCrear: IModal;
  setModalCrear: any;
  setStateDoctorsList: any;
  stateDoctorsListStatic: (IUser & { doctorDetail: ICreateDoctor })[];
  stateDoctorSelected: IUser & { doctorDetail: ICreateDoctor };
  setStateDoctorSelected: any;
}

const FormUpdateDoctor = (props: IFormUpdateDoctor) => {
  const [form] = Form.useForm<IUser & ICreateDoctor>();
  const { modalCrear, setModalCrear, setStateDoctorsList, stateDoctorSelected, setStateDoctorSelected } = props;
  let { stateDoctorsListStatic } = props;

  useEffect(() => {
    form.setFieldsValue({
      medicalCenterId: stateDoctorSelected.medicalCenterId,
      gender: stateDoctorSelected.gender,
      birthDate: moment(stateDoctorSelected.birthDate),
      firstName: stateDoctorSelected.firstName,
      lastName: stateDoctorSelected.lastName,
      email: stateDoctorSelected.email,
      dni: stateDoctorSelected.dni,
      specialty: stateDoctorSelected.doctorDetail.specialty,
      consultationRooms: stateDoctorSelected.doctorDetail.consultationRooms.slice(0, 1),
      collegiateNumber: stateDoctorSelected.doctorDetail.collegiateNumber,
    });
  }, [
    form,
    stateDoctorSelected.email,
    stateDoctorSelected.firstName,
    stateDoctorSelected.lastName,
    stateDoctorSelected.birthDate,
    stateDoctorSelected.gender,
    stateDoctorSelected.medicalCenterId,
    stateDoctorSelected.dni,
    stateDoctorSelected.doctorDetail.collegiateNumber,
    stateDoctorSelected.doctorDetail.consultationRooms,
    stateDoctorSelected.doctorDetail.specialty,
  ]);

  const onFinish = (values: IUser & ICreateDoctor) => {
    if (stateDoctorSelected.id) {
      doctorServices
        .updateDoctor(stateDoctorSelected.id, {
          user: {
            firstName: values.firstName,
            lastName: values.lastName,
            email: values.email,
            dni: values.dni,
            role: RoleType.DOCTOR,
            gender: values.gender,
            birthDate: values.birthDate,
            familyId: "",
            medicalCenterId: values.medicalCenterId,
          },
          doctorDetail: {
            specialty: values.specialty,
            collegiateNumber: values.collegiateNumber,
            consultationRooms: [`${values.consultationRooms}`],
          },
        })
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          const data_: IUser & ICreateDoctor = data.data;

          if (statusCode === 200) {
            Modal.info({
              title: "Muy bien",
              content: (
                <ul>
                  <li>Doctor actualizado</li>
                </ul>
              ),
            });
            form.resetFields();
            setModalCrear({ ...modalCrear, visible: false });
            setStateDoctorSelected(null);

            setStateDoctorsList((state: (IUser & { doctorDetail: IDoctorDetail })[]) =>
              state.map((user) => {
                if (user.id === data_.id) {
                  user.firstName = data_.firstName;
                  user.lastName = data_.lastName;
                  user.email = data_.email;
                  user.dni = data_.dni;
                  user.status = data_.status;
                  user.role = data_.role;
                  user.gender = data_.gender;
                  user.birthDate = data_.birthDate;
                  user.familyId = data_.familyId;
                  user.medicalCenterId = data_.medicalCenterId;
                  user.doctorDetail.specialty = data_.specialty;
                  if (typeof data_.consultationRooms === "string") {
                    user.doctorDetail.consultationRooms = JSON.parse(data_.consultationRooms);
                  }
                  user.doctorDetail.collegiateNumber = data_.collegiateNumber;
                }
                return user;
              })
            );

            stateDoctorsListStatic = stateDoctorsListStatic.map((user) => {
              if (user.id === data_.id) {
                user.firstName = data_.firstName;
                user.lastName = data_.lastName;
                user.email = data_.email;
                user.dni = data_.dni;
                user.status = data_.status;
                user.role = data_.role;
                user.gender = data_.gender;
                user.birthDate = data_.birthDate;
                user.familyId = data_.familyId;
                user.medicalCenterId = data_.medicalCenterId;
                user.doctorDetail.specialty = data_.specialty;
                if (typeof data_.consultationRooms === "string") {
                  user.doctorDetail.consultationRooms = JSON.parse(data_.consultationRooms);
                }
                user.doctorDetail.collegiateNumber = data_.collegiateNumber;
              }
              return user;
            });
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
    }
  };

  return (
    <Modal
      title={<strong>Actualizar Doctor</strong>}
      visible={modalCrear.visible}
      onCancel={() => {
        setModalCrear({ ...modalCrear, visible: false });
        setStateDoctorSelected(null);
      }}
      footer={false}
    >
      <Form
        form={form}
        labelCol={{ span: 24 }}
        wrapperCol={{ span: 24 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
      >
        <Row gutter={[10, 10]}>
          <Col lg={24}>
            <Form.Item
              name="medicalCenterId"
              label={<strong>Centro médico</strong>}
              rules={[{ required: true, message: "Selecciona el centro médico" }]}
            >
              <Select placeholder="Seleccione" allowClear showSearch>
                {medicalCenters.map((medicalCenter, index) => (
                  <Select.Option key={index} value={medicalCenter.id}>
                    {medicalCenter.name}
                  </Select.Option>
                ))}
              </Select>
            </Form.Item>
          </Col>

          <Col lg={12}>
            <Form.Item
              name="gender"
              label={<strong>Genero</strong>}
              rules={[{ required: true, message: "Selecciona el genero" }]}
            >
              <Select
                placeholder="Seleccione"
                // onChange={onGenderChange}
                allowClear
              >
                <Select.Option value="MALE">Masculino</Select.Option>
                <Select.Option value="FEMALE">Femenino</Select.Option>
              </Select>
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item
              name="birthDate"
              label={<strong>Fecha de nacimiento</strong>}
              rules={[{ required: true, message: "La fecha de nacimimiento es requerida" }]}
            >
              <DatePicker
                format={FORMAT_DATE_DD_MM_YYY}
                locale={locale}
                style={{ width: "100%" }}
                disabledDate={(currentDate: Moment) => {
                  return currentDate > moment().subtract(18, "years").endOf("day");
                }}
              />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item
              label={<strong>Nombre</strong>}
              name="firstName"
              rules={[{ required: true, message: "Nombre es requerido" }]}
            >
              <Input />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item
              label={<strong>Apellido</strong>}
              name="lastName"
              rules={[{ required: true, message: "Apellido es requerido" }]}
            >
              <Input />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item
              label={<strong>Email</strong>}
              name="email"
              rules={[
                { required: true, message: "Email es requerido" },
                { type: "email", message: "Escriba un email" },
              ]}
            >
              <Input />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item
              label={<strong>Repita email</strong>}
              name="re_email"
              dependencies={["email"]}
              hasFeedback
              rules={[
                { required: true, message: "Repita email es requerido" },
                { type: "email", message: "Escriba un email valido" },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    if (!value || getFieldValue("email") === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error("Verifica que el email es correcto. Hay una diferencia."));
                  },
                }),
              ]}
            >
              <Input />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item
              label={<strong>Cédula</strong>}
              name="dni"
              rules={[
                { required: true, message: "La cédula es requerida" },
                { max: 12, message: "Máximo 12 catacteres" },
              ]}
            >
              <Input />
            </Form.Item>
          </Col>
          <Col lg={24}>
            <Form.Item
              name="specialty"
              label={<strong>Especialidad</strong>}
              rules={[{ required: true, message: "Selecciona la especialidad" }]}
            >
              <Select placeholder="Seleccione" allowClear showSearch>
                {panama.map((especialidad, index) => (
                  <Select.Option key={index} value={especialidad}>
                    {especialidad}
                  </Select.Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col lg={24}>
            <Form.Item
              label={<strong>Dirección del consultorio principal</strong>}
              name="consultationRooms"
              rules={[{ required: true, message: "Indique la dirección del consultorio principal" }]}
            >
              <Input />
            </Form.Item>
          </Col>
          <Col lg={24}>
            <Form.Item
              label={<strong>Número colegiado</strong>}
              name="collegiateNumber"
              rules={[{ required: true, message: "Número colegiado es requerido" }]}
            >
              <Input />
            </Form.Item>
          </Col>
          <Col lg={24} style={{ textAlign: "center" }}>
            <Button type="primary" htmlType="submit">
              Actualizar Doctor
            </Button>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

export default FormUpdateDoctor;
