import { Alert, Popover, Typography } from "antd";
import ConsultationInfo from "../../../shared/components/ConsultationInfo";
import { IConsultation } from "../../../shared/interfaces/consultations.interfaces";
import moment from "moment";
import { FORMAT_TIME_H_mm } from "../../../shared/utils/formats";
import { getConsultationsTypeTitle } from "../../../shared/utils/consultations";

const ItemConsultationCancelled = (props: { consultation: IConsultation }) => {
  const { consultation } = props;

  const TitleDay = () => {
    return (
      <>
        <Typography.Text delete>Cancelada: {getConsultationsTypeTitle(consultation.type)}</Typography.Text>
      </>
    );
  };

  return (
    <Alert
      message={
        <Popover
          content={<ConsultationInfo consultation={consultation} />}
          title={getConsultationsTypeTitle(consultation.type)}
          trigger="click"
        >
          <Typography.Text
            style={{ width: "100%" }}
            ellipsis={{
              tooltip: false,
            }}
          >
            {`${moment(consultation.date).utc(false).format(FORMAT_TIME_H_mm)}`} <TitleDay />
          </Typography.Text>
        </Popover>
      }
      type="info"
      style={{
        paddingTop: 0,
        paddingLeft: 5,
        paddingBottom: 0,
        paddingRight: 0,
        fontSize: 11,
      }}
      className="item-calendar"
    />
  );
};

export default ItemConsultationCancelled;
