import { Button, Image, Modal, Space, Tag, Timeline, Typography } from "antd";
import moment from "moment";
import { useEffect, useState } from "react";
import { errors_translate_es } from "../../errors/es";
import { IPatient } from "../../interfaces/patients.interfaces";
import patientsServices from "../../services/patients.services";
import { v4 as uuidv4 } from "uuid";
import MedicalHistoryCreate from "../MedicalHistoryCreate";
import { IModal } from "../../interfaces/modals.interfaces";
import { IMedicalHistory } from "../../interfaces/medical-history.interfaces";
import { Auth } from "../../utils/auth";
import { RoleType, RoleTypeEmail } from "../../enum/roletype.enum";
import { TypeResponse, TypeResponseError } from "../../types/response.type";

const MedicalHistory = (props: { patientSelected?: IPatient }) => {
  const tokenDecode = Auth.tokenDecode();
  const { patientSelected } = props;
  const [stateHistory, setStateHistory] = useState<IMedicalHistory[]>([]);
  const [modalCrearHistory, setModalCrearHistory] = useState<IModal>({
    visible: false,
  });

  useEffect(() => {
    if (typeof patientSelected === "object") {
      patientsServices
        .getMedicalHistory(patientSelected.id)
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            setStateHistory(data);
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;
          if (statusCode === 409) {
            Modal.warning({
              content: errors_translate_es[message],
            });
          } else if (statusCode === 404) {
            setStateHistory([]);
          } else {
            Modal.error({
              title: "Hola",
              content: `${message} (${statusCode} ${error})`,
            });
          }
        });
    }
  }, [patientSelected]);

  if (!patientSelected) {
    return <p>Seleccione un paciente</p>;
  }

  return (
    <>
      <MedicalHistoryCreate
        idPatient={patientSelected.id}
        visible={modalCrearHistory.visible}
        setModalCrearHistory={setModalCrearHistory}
        setStateHistory={setStateHistory}
      />

      <Space align="center" style={{ justifyContent: "space-between", display: "flex", marginTop: 5 }}>
        <strong>
          Historial Clínico de {patientSelected.firstName} {patientSelected.lastName}
        </strong>
        {tokenDecode.role === RoleType.DOCTOR && (
          <Button
            type="primary"
            onClick={() => {
              setModalCrearHistory({ visible: true });
            }}
          >
            Actualizar historial clínico
          </Button>
        )}
      </Space>
      <br />
      <p>
        <Typography.Text strong>Nacimiento:</Typography.Text>{" "}
        {moment(patientSelected.birthDate).format("D [de] MMMM [del] YYYY")} <br />
        {!patientSelected.email.includes(RoleTypeEmail.PATIENT_FAMILY) && (
          <>
            <Typography.Text strong>Email:</Typography.Text> {patientSelected.email} <br />
          </>
        )}
        <Typography.Text strong>Cédula:</Typography.Text> {patientSelected.dni ? patientSelected.dni : "No indicado"}{" "}
        <br />
        <Typography.Text strong>Estatus en el sistema:</Typography.Text> {patientSelected.status.toLowerCase()}
        {/* <strong>Pasaporte:</strong> {patientSelected.passport} */}
      </p>
      <br />
      {stateHistory.length < 1 && (
        <div>
          <Typography.Text strong>Historial:</Typography.Text>No indicó
        </div>
      )}
      <Timeline>
        {stateHistory.map((history, index) => (
          <Timeline.Item key={index}>
            <Tag>{moment(history.createdAt).format("D [de] MMMM [del] YYYY [a las] h:mm A")}</Tag>
            <br />
            <br />
            {JSON.parse(history.history).map((history: { title: string; observations: string }) => (
              <div key={uuidv4()}>
                <strong>{history.title}:</strong> {history.observations}
              </div>
            ))}

            <>
              {history.files?.pdfs.length || history.files?.images.length ? (
                <>
                  <strong>Adjuntos:</strong>
                  <div style={{ height: 5 }}></div>
                </>
              ) : null}

              <Space direction="vertical">
                <Space direction="vertical">
                  {history.files?.pdfs.map((pdf) => (
                    <div key={pdf}>
                      <a
                        target="_blank"
                        rel="noreferrer"
                        href={`${process.env.REACT_APP_FILES_URL}/medical-history-file/${pdf}`}
                      >
                        {pdf}
                      </a>
                    </div>
                  ))}
                </Space>
                <Space direction="horizontal">
                  {history.files?.images.map((image) => (
                    <Image
                      key={image}
                      width={60}
                      height={60}
                      src={`${process.env.REACT_APP_FILES_URL}/medical-history-file/${image}`}
                    />
                  ))}
                </Space>
              </Space>
            </>
          </Timeline.Item>
        ))}
      </Timeline>
    </>
  );
};

export default MedicalHistory;
