import { Button, Tooltip } from "antd";
import { StopOutlined } from "@ant-design/icons";

type TypeButtonCancel = {
  onClick: () => void;
};

const ButtonCancel = (props: TypeButtonCancel) => {
  const { onClick } = props;
  return (
    <Tooltip title="Cancelar">
      <Button
        type="dashed"
        danger
        shape="circle"
        icon={<StopOutlined />}
        size="small"
        onClick={() => {
          onClick();
        }}
      />
    </Tooltip>
  );
};

export default ButtonCancel;
