import { Alert, Popover, Space, Typography } from "antd";
import "moment/locale/es";
import moment from "moment";
import ButtonAttend from "../../../shared/components/ButtonAttend";
import ConsultationInfo from "../../../shared/components/ConsultationInfo";
import { IConsultation } from "../../../shared/interfaces/consultations.interfaces";
import { FORMAT_TIME_H_mm } from "../../../shared/utils/formats";
import { getConsultationsTypeTitle } from "../../../shared/utils/consultations";
import ButtonReschedule from "../../../shared/components/ButtonReschedule";
import ButtonCancel from "../../../shared/components/ButtonCancel";
import { ConsultationsType } from "../../../shared/enum/consultations-type.enum";
import "./../../../shared/styles/calendar.css";

type TypeItemConsultationPending = {
  consultation: IConsultation;
  setModalReschedule: any;
  setModalCancel: any;
  setStateSelected: any;
};

const ItemConsultationPending = (props: TypeItemConsultationPending) => {
  const { consultation, setModalCancel, setStateSelected, setModalReschedule } = props;

  const ToolTip = () => (
    <div style={{ zIndex: 1 }}>
      <ConsultationInfo consultation={consultation} />
      <hr style={{ marginBottom: 10 }} />
      <Space direction="horizontal">
        <ButtonAttend size="small" consultation={consultation} />
        {(consultation.type === ConsultationsType.FACE_TO_FACE ||
          consultation.type === ConsultationsType.SURGERY ||
          consultation.type === ConsultationsType.ONLINE) && (
          <ButtonReschedule
            onClick={() => {
              setStateSelected(consultation);
              setModalReschedule({ visible: true });
            }}
          />
        )}

        <ButtonCancel
          onClick={() => {
            setStateSelected(consultation);
            setModalCancel({ visible: true });
          }}
        />
      </Space>
    </div>
  );

  return (
    <>
      <Alert
        message={
          <Popover
            content={ToolTip}
            title={getConsultationsTypeTitle(consultation.type)}
            trigger="click"
            style={{ zIndex: 2 }}
          >
            <Typography.Text
              style={{ width: "100%" }}
              ellipsis={{
                tooltip: false,
              }}
            >
              {`${moment(consultation.date).utc(false).format(FORMAT_TIME_H_mm)}`}{" "}
              {getConsultationsTypeTitle(consultation.type)}
            </Typography.Text>
          </Popover>
        }
        type="info"
        style={{
          paddingTop: 0,
          paddingLeft: 5,
          paddingBottom: 0,
          paddingRight: 0,
          fontSize: 11,
        }}
        className="item-calendar"
      />
    </>
  );
};

export default ItemConsultationPending;
