import { Form, Input, Button, Row, Col, Modal, Spin } from "antd";
import { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { errors_translate_es } from "../../../shared/errors/es";
import authServices from "../../../shared/services/auth.services";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";

const NuevaClave = () => {
  const location = useLocation();
  const [form] = Form.useForm();
  const [statePageExpired, setStatePageExpired] = useState<boolean>(false);
  const [stateUserName, setStateUserName] = useState<string>("");
  const [stateChangePassword, setStateChangePassword] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);

  const query = new URLSearchParams(location.search);
  const userId = query.get("u");
  const email = query.get("e");
  const tokenPasswordRecovery = query.get("t");
  const userName = query.get("n");

  useEffect(() => {
    if (userId && email && tokenPasswordRecovery && userName) {
      setStateUserName(userName);
    }
  }, [userId, email, tokenPasswordRecovery, userName]);

  const onFinish = (values: { password: string }) => {
    if (userId && email && tokenPasswordRecovery && userName) {
      setLoading(true);
      authServices
        .recoveryPasswordByEmail({ userId, email, tokenPasswordRecovery, password: values.password })
        .then(async (response: TypeResponse) => {
          const { statusCode, data } = response;

          if (statusCode === 200) {
            setStatePageExpired(false);
            setLoading(false);
            setStateUserName(data.user.firstName);
            setStateChangePassword(true);
          }
        })
        .catch((errors: TypeResponseError) => {
          setLoading(false);
          const { error, message, statusCode } = errors;
          if (statusCode === 409) {
            Modal.warning({
              title: "Hola",
              content: errors_translate_es[message],
            });
          } else {
            Modal.error({
              title: "Hola",
              content: `${message} (${statusCode} ${error})`,
            });
          }
        });
    }
  };

  if (!query.get("u") || !query.get("e") || !query.get("t") || !query.get("n")) {
    return (
      <Row justify="center" align="middle">
        <Col lg={7}>
          <br />
          <br />
          <div
            style={{
              border: "0.5px solid #00285f",
              borderRadius: 5,
              padding: 20,
            }}
          >
            <div style={{ textAlign: "center" }}>
              <div>Bad request</div>
              <div>
                Si cree que está siguiendo los pasos de manera correcta y aún se muestra esta información, escribanos a
                ayuda@tuhospitalvirtual.com
              </div>

              <br />
              <Link to="/auth/login">Ir al login</Link>
            </div>
          </div>
        </Col>
      </Row>
    );
  }

  if (stateChangePassword) {
    return (
      <Row justify="center" align="middle">
        <Col lg={7}>
          <br />
          <br />
          <div
            style={{
              border: "0.5px solid #00285f",
              borderRadius: 5,
              padding: 20,
            }}
          >
            <div style={{ textAlign: "center" }}>
              <div>
                <strong>Listo</strong> {stateUserName}, en el login <strong>ingresa con tu nueva clave</strong>
              </div>
              <br />
              <Link to="/auth/login">Ir al login</Link>
            </div>
          </div>
        </Col>
      </Row>
    );
  }

  return (
    <Row justify="center" align="middle">
      <Col lg={7}>
        <br />
        <br />
        <div
          style={{
            border: "0.5px solid #00285f",
            borderRadius: 5,
            padding: 20,
          }}
        >
          {statePageExpired ? (
            <div style={{ textAlign: "center" }}>
              <div>
                <strong>Hola, esta opción ha expirado.</strong>
              </div>
              <div>
                Si cree que está siguiendo los pasos de manera correcta y aún se muestra esta información, escribanos a
                ayuda@tuhospitalvirtual.com
              </div>

              <br />
              <Link to="/auth/login">Volver al login</Link>
            </div>
          ) : (
            <Spin tip="Estamos validando tus datos" spinning={loading}>
              <Form
                form={form}
                labelCol={{ span: 24 }}
                wrapperCol={{ span: 24 }}
                onFinish={onFinish}
                initialValues={{ remember: true }}
              >
                <div>Hola{stateUserName.length < 1 ? "" : ` ${stateUserName}`}, escriba su nueva clave.</div>
                <br />
                <Form.Item
                  label={<strong>Clave</strong>}
                  name="password"
                  rules={[
                    { required: true, message: "Escriba la nueva clave" },
                    {
                      min: 6,
                      message:
                        "La clave debe tener minimo 6 caracteres y debe de estar conformada por letras y números. Esto es por su seguridad",
                    },
                  ]}
                >
                  <Input.Password
                    onChange={() => {
                      form.resetFields(["re_password"]);
                    }}
                  />
                </Form.Item>

                <Form.Item
                  label={<strong>Repita la nueva clave</strong>}
                  name="re_password"
                  dependencies={["password"]}
                  hasFeedback
                  rules={[
                    { required: true, message: "Escriba de nuevo la nueva clave" },
                    {
                      min: 6,
                      message:
                        "La clave debe tener minimo 6 caracteres y debe de estar conformada por letras y números. Esto es por su seguridad",
                    },
                    ({ getFieldValue }) => ({
                      validator(_, value) {
                        if (!value || getFieldValue("password") === value) {
                          return Promise.resolve();
                        }
                        return Promise.reject(new Error("Verifica las claves. Son distintas."));
                      },
                    }),
                  ]}
                >
                  <Input.Password />
                </Form.Item>

                <br />

                <div style={{ textAlign: "center" }}>
                  <Button type="primary" htmlType="submit">
                    Cambiar clave
                  </Button>
                  <br />
                  <br />
                  <Link to="/auth/login">Volver al login</Link>
                </div>
              </Form>
            </Spin>
          )}
        </div>
      </Col>
    </Row>
  );
};

export default NuevaClave;
