import { useEffect, useState } from "react";
import { Card, Radio, Form, Button, InputNumber, TimePicker, Row, Col, Modal, Typography } from "antd";
import jwt_decode from "jwt-decode";
import moment from "moment";
import "moment/locale/es";
import locale from "antd/es/date-picker/locale/es_ES";
import doctorServices from "../../../shared/services/doctor.services";
import { errors_translate_es } from "../../../shared/errors/es";
import { IScheduleDay } from "../../../shared/interfaces/doctor.interfaces";
import { FORMAT_TIME_HH_mm } from "../../../shared/utils/formats";
import { Auth } from "../../../shared/utils/auth";

type FormEdit = {
  isAttendInquiries: "S" | "N";
  from: any;
  until: any;
  numberConsultations: number;
};

const HorarioDetail = (props: IScheduleDay & { setStateSchedule: any }): JSX.Element => {
  const { day, active, from, until, numberConsultations, setStateSchedule } = props;

  const [formEdit] = Form.useForm();
  const [isAttendInquiries, setIsAttendInquiries] = useState<"S" | "N">(active ? "S" : "N");
  const [stateShowModalEdit, setStateShowModalEdit] = useState<boolean>(false);
  const titleEditDay = day.charAt(0).toUpperCase() + day.slice(1, day.length).toLowerCase();

  const onFinish = (values: FormEdit) => {
    const tokenDecode: { id: string } = jwt_decode(window.sessionStorage.getItem("token") || "");

    doctorServices
      .updateSchedule(tokenDecode.id, {
        day,
        active: values.isAttendInquiries === "S",
        from: values.from ? moment(values.from, "HH:mm").format("HH:mm") : "00:00",
        until: values.until ? moment(values.until, "HH:mm").format("HH:mm") : "00:00",
        numberConsultations: values.numberConsultations || 1,
      })
      .then((response: any) => {
        const { statusCode, data } = response;
        if (statusCode === 200) {
          setStateSchedule(data);
          setStateShowModalEdit(false);
        }
      })
      .catch((errors) => {
        const { error, message, statusCode } = errors;
        if (statusCode === 404) {
          Modal.warning({
            content: errors_translate_es[message],
          });
        } else {
          Modal.error({
            content: `${message} (${statusCode} ${error})`,
          });
        }
      });
  };

  return (
    <>
      <Card size="small" title={<span>{titleEditDay}</span>}>
        {active ? (
          <>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <strong>Desde: </strong> &nbsp;&nbsp;&nbsp;
              <div style={{ minWidth: 70 }}>{moment(from, "H:mm").format("h:mm A")}</div>
            </div>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <strong>Hasta: </strong> &nbsp;&nbsp;&nbsp;
              <div style={{ minWidth: 70 }}>{moment(until, "H:mm").format("h:mm A")}</div>
            </div>
            <div>{numberConsultations} consultas</div>
          </>
        ) : (
          <>
            <div style={{ textAlign: "center" }}>
              <div>
                <Typography.Text type="secondary" strong>
                  No hay nada planeado para el día
                </Typography.Text>
              </div>
              <div>
                <Typography.Text type="secondary">¡Disfrútelo!</Typography.Text>
              </div>
            </div>
          </>
        )}

        <br />
        <div style={{ textAlign: "center" }}>
          <Button
            type="primary"
            ghost={active}
            onClick={() => {
              setStateShowModalEdit(true);
            }}
          >
            Editar
          </Button>
        </div>
        {/* </>
        )} */}
      </Card>

      <Modal
        title={titleEditDay}
        visible={stateShowModalEdit}
        onCancel={() => {
          setStateShowModalEdit(false);
        }}
        footer={false}
      >
        <Form
          form={formEdit}
          wrapperCol={{ span: 24 }}
          labelCol={{ span: 24 }}
          initialValues={{
            from: moment(from, "H:mm"),
            until: moment(until, "H:mm"),
            numberConsultations,
            isAttendInquiries,
          }}
          onFinish={onFinish}
        >
          <Row gutter={[10, 10]}>
            <Col lg={24}>
              <Form.Item name="isAttendInquiries">
                <Radio.Group
                  onChange={(e) => {
                    setIsAttendInquiries(e.target.value);
                  }}
                >
                  <Radio value="S">Si</Radio>
                  <Radio value="N">No</Radio>
                </Radio.Group>
              </Form.Item>
            </Col>
            {isAttendInquiries === "S" && (
              <>
                <Col lg={10}>
                  <Form.Item
                    name="from"
                    label={<strong>Desde</strong>}
                    rules={[{ required: true, message: "Coloca desde que hora empezarás a atender" }]}
                  >
                    <TimePicker format={FORMAT_TIME_HH_mm} locale={locale} style={{ width: "100%" }} />
                  </Form.Item>
                </Col>
                <Col lg={10}>
                  <Form.Item
                    name="until"
                    label={<strong>Hasta</strong>}
                    rules={[{ required: true, message: "Coloca hasta que hora a atenderás" }]}
                  >
                    <TimePicker format={FORMAT_TIME_HH_mm} locale={locale} style={{ width: "100%" }} />
                  </Form.Item>
                </Col>
                <Col lg={24}>
                  <Form.Item
                    name="numberConsultations"
                    label={<strong>Cantidad de consultas que atenderás</strong>}
                    rules={[{ required: true, message: "Coloca la cantidad de consultas que atenderás" }]}
                  >
                    <InputNumber min={1} max={50} />
                  </Form.Item>
                </Col>
              </>
            )}
          </Row>

          <br />
          <div style={{ textAlign: "center" }}>
            <Button type="primary" htmlType="submit">
              Editar horario
            </Button>
          </div>
        </Form>
      </Modal>
    </>
  );
};

const Horarios = () => {
  const [stateSchedule, setStateSchedule] = useState<IScheduleDay[]>([]);

  const tokenDecode = Auth.tokenDecode();

  useEffect(() => {
    if (tokenDecode.id) {
      doctorServices
        .getSchedule(tokenDecode.id)
        .then((response: any) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            setStateSchedule(data);
          }
        })
        .catch((errors) => {
          const { error, message, statusCode } = errors;
          if (statusCode === 404) {
            Modal.warning({
              content: errors_translate_es[message],
            });
          } else {
            Modal.error({
              content: `${message} (${statusCode} ${error})`,
            });
          }
        });
    }
  }, [tokenDecode.id]);

  return (
    <>
      {/* <Space align="start"> */}
      <Row gutter={[10, 10]}>
        {stateSchedule.map((schedule) => (
          <Col xs={24} sm={8} md={8} lg={4} key={schedule.day}>
            <HorarioDetail
              day={schedule.day}
              active={schedule.active}
              from={schedule.from}
              until={schedule.until}
              numberConsultations={schedule.numberConsultations}
              setStateSchedule={setStateSchedule}
            />
          </Col>
        ))}
      </Row>

      {/* </Space> */}
    </>
  );
};

export default Horarios;
