import { Typography } from "antd";
import { DayCodeMoment } from "../../../shared/enum/day.enum";
import { IScheduleDay } from "../../../shared/interfaces/doctor.interfaces";
import { getDetailsScheduleDay } from "../../../shared/utils/schedule";

interface IBtnSchedule {
  doctorSchedule: IScheduleDay[];
  dayCode: DayCodeMoment;
}

const FTextScheduleDay = (props: IBtnSchedule) => {
  const { doctorSchedule, dayCode } = props;

  const details = getDetailsScheduleDay(doctorSchedule, dayCode);

  if (!details) {
    return <div>Seleccione la fecha</div>;
  }

  const TextDay = () => (
    <Typography.Text strong>
      {details.day.charAt(0)}
      {details.day.slice(1, details.day.length).toLowerCase()}:
    </Typography.Text>
  );

  if (!details.active) {
    return (
      <div>
        <TextDay /> Libre
      </div>
    );
  }

  return (
    <div>
      <Typography.Text mark>
        <TextDay /> {details.from} a {details.until}.
      </Typography.Text>{" "}
      <Typography.Text>Tope: {details.numberConsultations} consultas</Typography.Text>
    </div>
  );
};

export default FTextScheduleDay;
