import { Tooltip, Col, DatePicker, Form, TimePicker, FormInstance, Button, Alert } from "antd";
import moment, { Moment } from "moment";
import { FORMAT_DATE_DD_MM_YYY, FORMAT_TIME_HH_mm } from "../../utils/formats";
import locale from "antd/es/date-picker/locale/es_ES";
import {
  buildDisabledHoursByDay,
  buildDisabledMinutesByDay,
  buildDisabledDaysDoctorSchedule,
  disabledDates,
} from "../../utils/schedule";
import BtnSchedule from "../../../apps/doctor/Calendar/BtnSchedule";
import TextScheduleDay from "../../../apps/doctor/Calendar/TextScheduleDay";
import { useEffect, useState } from "react";
import { TypeResponse, TypeResponseError } from "../../types/response.type";
import { IScheduleDay } from "../../interfaces/doctor.interfaces";
import doctorServices from "../../services/doctor.services";
import { ConsultationsType } from "../../enum/consultations-type.enum";

type TypeFormItemDateHour = {
  doctorId: string;
  formCreateConsultation: FormInstance<any>;
  consultationType: ConsultationsType;
};

const FormItemDateHour = (props: TypeFormItemDateHour) => {
  const { formCreateConsultation, consultationType, doctorId } = props;

  const [stateDisabledDaysDoctorSchedule, setStateDisabledDaysDoctorSchedule] = useState<number[]>([]);
  const [stateDisabledHoursByDay, setStateDisabledHoursByDay] = useState<number[]>([]);
  const [stateDisabledMinutesByDay, setStateDisabledMinutesByDay] = useState<number[]>([]);
  const [stateDisabledDate, setStateDisabledDate] = useState<boolean>(true);
  const [stateDisabledHour, setStateDisabledHour] = useState<boolean>(true);
  const [stateDayCode, setStateDayCode] = useState<number>(1000);
  const [stateSchedule, setStateSchedule] = useState<IScheduleDay[]>([]);

  useEffect(() => {
    if (doctorId) {
      doctorServices
        .getSchedule(doctorId)
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            setStateDisabledDate(false);
            setStateSchedule(data);
            setStateDisabledDaysDoctorSchedule(buildDisabledDaysDoctorSchedule(data));
          }
        })
        .catch((errors: TypeResponseError) => {
          setStateDisabledDate(true);
          const { error, message, statusCode } = errors;
          console.log(`${message} (${statusCode} ${error})`);

          // Modal.error({
          //   content: `${message} (${statusCode} ${error})`,
          // });
        });
    }
  }, [doctorId]);

  function onChangeDate(date: Moment | null, dateString: string) {
    if (dateString.length > 1) {
      setStateDisabledHour(false);
      if (date instanceof moment) {
        setStateDisabledHoursByDay(buildDisabledHoursByDay(stateSchedule, date.day(), date));
        setStateDisabledMinutesByDay(buildDisabledMinutesByDay(stateSchedule, date.day(), date));
        setStateDayCode(date.day());
      }
    } else {
      setStateDisabledHour(true);
      setStateDayCode(1000);
      formCreateConsultation.resetFields(["hour"]);
    }

    // TODO: puediera ser aqui donde se valida si hay otra consulta, o hay mas consultas para esa fecha
    // no se,, aunque se debieran bloquear las fechas no disponibles de una vez,,
    // pero puede ser que esa fecha ya se hlla tomado mientras llenaba ? mmm no se
  }

  return (
    <>
      {(consultationType === ConsultationsType.FACE_TO_FACE ||
        consultationType === ConsultationsType.SURGERY ||
        consultationType === ConsultationsType.ONLINE) && (
        <Col lg={24}>
          {stateDisabledDate ? (
            <Alert
              message={
                <>
                  <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                    No hay horarios disponibles para este doctor
                  </div>
                </>
              }
              type="error"
            />
          ) : (
            <Alert
              message={
                <>
                  <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                    <TextScheduleDay doctorSchedule={stateSchedule} dayCode={stateDayCode} />
                    <Tooltip title={<BtnSchedule doctorSchedule={stateSchedule} />}>
                      <Button type="primary" ghost>
                        Horarios
                      </Button>
                    </Tooltip>
                  </div>
                </>
              }
              type="info"
            />
          )}

          <br />
        </Col>
      )}

      <Col lg={10}>
        <Form.Item
          name="date"
          label={<strong>Fecha</strong>}
          rules={[{ required: true, message: "La fecha es requerida" }]}
        >
          <DatePicker
            disabled={stateDisabledDate}
            format={FORMAT_DATE_DD_MM_YYY}
            onChange={onChangeDate}
            locale={locale}
            style={{ width: "100%" }}
            disabledDate={(currentDate: Moment) => {
              return (
                currentDate < moment().subtract(1, "day").endOf("day") ||
                currentDate > moment().add(3, "months") ||
                stateDisabledDaysDoctorSchedule.includes(moment(currentDate).day()) ||
                disabledDates.some((date) => date === moment(currentDate).format("YYYY-MM-DD"))
              );
            }}
          />
        </Form.Item>
      </Col>
      <Col lg={10}>
        <Form.Item
          name="hour"
          label={<strong>Hora</strong>}
          rules={[{ required: true, message: "La hora es requerida" }]}
        >
          <TimePicker
            disabled={stateDisabledHour}
            format={FORMAT_TIME_HH_mm}
            locale={locale}
            disabledHours={() => {
              return stateDisabledHoursByDay;
            }}
            disabledMinutes={() => {
              return stateDisabledMinutesByDay;
            }}
            style={{ width: "100%" }}
          />
        </Form.Item>
      </Col>
    </>
  );
};

export default FormItemDateHour;
