import { Form, Input, Button, Checkbox, Row, Col, Modal } from "antd";
import { useHistory, Link } from "react-router-dom";
import { RoleType } from "../../../shared/enum/roletype.enum";
import { errors_translate_es } from "../../../shared/errors/es";
import authServices from "../../../shared/services/auth.services";

const Login = () => {
  const history = useHistory();

  const onFinish = (values: any) => {
    authServices
      .login({ username: values.email, password: values.clave })
      .then((response) => {
        const {
          statusCode,
          data: { token, payload },
        } = response;

        if (statusCode === 200) {
          window.sessionStorage.setItem("token", token);
          if (payload.role === RoleType.ADMIN) {
            history.push(`/admin`);
          } else if (payload.role === RoleType.DOCTOR) {
            history.push(`/doctor`);
          } else if (payload.role === RoleType.PATIENT) {
            history.push(`/patient`);
          }
        }
      })
      .catch((errors) => {
        const { error, message, statusCode } = errors;
        if (statusCode === 409) {
          Modal.warning({
            title: "Hola",
            content: errors_translate_es[message],
          });
        } else {
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        }
      });
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log("Failed:", errorInfo);
  };

  return (
    <Row justify="center" align="middle">
      <Col lg={7}>
        <br />
        <br />
        <div
          style={{
            border: "0.5px solid #00285f",
            borderRadius: 5,
            padding: 20,
          }}
        >
          <Form
            name="basic"
            labelCol={{ span: 24 }}
            wrapperCol={{ span: 24 }}
            initialValues={{ remember: true }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
          >
            <Form.Item label="Email" name="email" rules={[{ required: true, message: "Escribe tu email" }]}>
              <Input />
            </Form.Item>

            <Form.Item label="Clave" name="clave" rules={[{ required: true, message: "Escribe tu clave" }]}>
              <Input.Password />
            </Form.Item>
            <div style={{ textAlign: "right" }}>
              <Link to="/auth/olvide-clave">Olvidé mi clave</Link>
            </div>
            <Form.Item name="remember" valuePropName="checked" wrapperCol={{ offset: 8, span: 16 }}>
              <Checkbox>Recuerdame</Checkbox>
            </Form.Item>

            <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
              <Button type="primary" htmlType="submit">
                Entrar
              </Button>
            </Form.Item>
          </Form>
        </div>
      </Col>
    </Row>
  );
};

export default Login;
