import requestApiConsultations from "../../config/requestApiConsultations";
import { IFilesUpload } from "../interfaces/files.interfaces";

interface FilesServices {
  uploadFiles(filesUpload: IFilesUpload): Promise<any>;
  getAttachedFilesByConsultationId(consultationId: string): Promise<any>;
}

const filesServices: FilesServices = {
  uploadFiles: function (filesUpload) {
    return requestApiConsultations({
      method: "POST",
      url: "/files",
      data: filesUpload,
      private: true,
    });
  },
  getAttachedFilesByConsultationId: function (consultationId) {
    return requestApiConsultations({
      method: "GET",
      url: `/files/consultations/${consultationId}`,
      private: true,
    });
  },
};

export default filesServices;
