import { useEffect, useState } from "react";
import "moment/locale/es";
import moment, { Moment } from "moment";
import { Alert, Button, Col, Form, Input, Modal, Radio, Row, Select, Space, Typography, Upload } from "antd";
import { UploadOutlined } from "@ant-design/icons";
import patientsServices from "../../services/patients.services";
import consultationsServices from "../../services/consultations.services";
import { ConsultationsType } from "../../enum/consultations-type.enum";
import { errors_translate_es } from "../../errors/es";
import { IPatient } from "../../interfaces/patients.interfaces";
import { ConsultationsStatus } from "../../enum/consultations-status.enum";
import { IConsultation } from "../../interfaces/consultations.interfaces";
import { IFile, IFilesUpload } from "../../interfaces/files.interfaces";
import { FileExtension, FileContext } from "../../enum/files.enum";
import filesServices from "../../services/files.services";
import { Auth } from "../../utils/auth";
import FormItemDateHour from "./FormItemDateHour";
import { IUser } from "../../interfaces/user.interfaces";
import doctorServices from "../../services/doctor.services";
import { RoleType } from "../../enum/roletype.enum";
import { TypeResponse, TypeResponseError } from "../../types/response.type";
import { getBase64 } from "../../utils/getBase64";

const FormCreateConsultation = (props: {
  type: ConsultationsType;
  setModalCrearEvento: any;
  setStateListConsultations: any;
}) => {
  const tokenDecode = Auth.tokenDecode();

  const { type, setModalCrearEvento, setStateListConsultations } = props;
  const [formCreateConsultation] = Form.useForm();
  const [statePatientsList, setStatePatientsList] = useState<IPatient[]>([]);
  const [stateDoctorsList, setStateDoctorsList] = useState<IUser[]>([]);
  const [stateConsultationType, setStateConsultationType] = useState<ConsultationsType>(ConsultationsType.FACE_TO_FACE);
  const [seletedDoctorId, setSeletedDoctorId] = useState<string | null>(null);
  const [isFamily, setIsFamily] = useState<boolean>(false);
  const [stateFamilyList, setStateFamilyList] = useState<IUser[]>([]);

  useEffect(() => {
    if (tokenDecode.role === RoleType.DOCTOR) {
      if (tokenDecode.id) setSeletedDoctorId(tokenDecode.id);

      patientsServices
        .getList()
        .then((response: any) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            setStatePatientsList(data);
          }
        })
        .catch((errors) => {
          const { error, message, statusCode } = errors;
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
    }
    if (tokenDecode.role === RoleType.PATIENT) {
      doctorServices
        .getDoctors()
        .then((response: any) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            setStateDoctorsList(data);
          }
        })
        .catch((errors) => {
          const { error, message, statusCode } = errors;
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
      if (tokenDecode.id) {
        patientsServices
          .getListFamily(tokenDecode.id)
          .then((response: TypeResponse) => {
            const { statusCode, data } = response;
            if (statusCode === 200) {
              setStateFamilyList(data);
              // statePatientsListStatic = data;
            }
          })
          .catch((errors: TypeResponseError) => {
            const { error, message, statusCode } = errors;
            if (statusCode === 409) {
              Modal.warning({
                content: errors_translate_es[message],
              });
            } else {
              Modal.error({
                title: "Hola",
                content: `${message} (${statusCode} ${error})`,
              });
            }
          });
      }
    }
  }, [tokenDecode.role, tokenDecode.id]);

  const onFinish = async (values: {
    type: ConsultationsType;
    date: Moment;
    hour: Moment;
    place: string;
    patient: string | undefined;
    doctor: string | undefined;
    observations: string;
    files: { originFileObj: any; type: "image/png" | "image/jpeg" | "application/pdf" }[];
    family: string | undefined;
  }) => {
    const date = moment(values.date).format("YYYY-MM-DD") + " " + moment(values.hour).format("HH:mm");

    const files_: IFile[] = [];
    if (values.files) {
      for (const file of values.files) {
        let extension = FileExtension.PNG;
        if (file.type === "image/png") {
          extension = FileExtension.PNG;
        } else if (file.type === "image/jpeg") {
          extension = FileExtension.JPEG;
        } else if (file.type === "application/pdf") {
          extension = FileExtension.PDF;
        }

        let base64 = await getBase64(file.originFileObj);
        base64 = base64.split(";base64,").pop();

        files_.push({
          extension,
          base64,
        });
      }
    }

    if (tokenDecode.id) {
      if (tokenDecode.role === RoleType.DOCTOR && values.patient) {
        consultationsServices
          .create({
            doctorId: tokenDecode.id,
            patientId: values.patient,
            status: ConsultationsStatus.PENDING,
            type: values.type || type,
            familyId: "",
            date: moment(date).utc(true).toDate(),
            place: values.place || "",
            observations: [`${values.observations}`],
            prescriptions: [],
            exams: [],
            quote: [],
          })
          .then((response: { statusCode: number; data: IConsultation }) => {
            const { statusCode, data } = response;
            if (statusCode === 201) {
              formCreateConsultation.resetFields();

              Modal.info({
                title: "Muy bien",
                content: "Consulta creada",
              });
              setModalCrearEvento({ title: "", visible: false });

              setStateListConsultations((consultations: IConsultation[]): IConsultation[] => [
                ...consultations,
                {
                  id: data.id,
                  status: data.status,
                  type: data.type,
                  date: data.date,
                  observations: data.observations,
                  place: data.place,
                  patient: data.patient,
                },
              ]);

              // BEGIN UPLOAD FILES
              if (files_.length > 0) {
                if (tokenDecode.id) {
                  const filesDB: IFilesUpload = {
                    patientId: "",
                    doctorId: "",
                    consultationId: data.id,
                    medicalHistoryId: "",
                    context: FileContext.CONSULTATION,
                    files: files_,
                  };

                  if (tokenDecode.role === RoleType.DOCTOR && values.patient) {
                    filesDB.patientId = values.patient;
                    filesDB.doctorId = tokenDecode.id;
                  }

                  if (tokenDecode.role === RoleType.PATIENT && values.doctor) {
                    filesDB.patientId = tokenDecode.id;
                    filesDB.doctorId = values.doctor;
                  }

                  if (filesDB.patientId.length === 0 || filesDB.doctorId.length === 0) {
                    return;
                  }

                  filesServices
                    .uploadFiles(filesDB)
                    .then((response: { statusCode: number; data: IConsultation }) => {
                      const { statusCode } = response;
                      if (statusCode === 201) {
                        Modal.info({
                          title: "Muy bien",
                          content: "Documentación adjunta",
                        });
                      }
                    })
                    .catch((errors) => {
                      const { error, message, statusCode } = errors;
                      if (statusCode === 409) {
                        Modal.warning({
                          content: errors_translate_es[message],
                        });
                      } else {
                        Modal.error({
                          content: `${message} (${statusCode} ${error})`,
                        });
                      }
                    });
                }
              }
              // END UPLOAD FILES
            }
          })
          .catch((errors: TypeResponseError) => {
            const { error, message, statusCode } = errors;
            Modal.error({
              content: `${message} (${statusCode} ${error})`,
            });
          });
      } else if (tokenDecode.role === RoleType.PATIENT && values.doctor) {
        let patientId = tokenDecode.id;
        if (values.family) {
          patientId = values.family;
        }

        consultationsServices
          .create({
            doctorId: values.doctor,
            patientId,
            status: ConsultationsStatus.PENDING,
            type: values.type || type,
            familyId: "",
            date: moment(date).utc(true).toDate(),
            place: values.place || "",
            observations: [`${values.observations}`],
            prescriptions: [],
            exams: [],
            quote: [],
          })
          .then((response: { statusCode: number; data: IConsultation }) => {
            const { statusCode, data } = response;
            if (statusCode === 201) {
              formCreateConsultation.resetFields();

              Modal.info({
                title: "Muy bien",
                content: "Consulta creada",
              });
              setModalCrearEvento({ title: "", visible: false });

              setStateListConsultations((consultations: IConsultation[]): IConsultation[] => [
                ...consultations,
                {
                  id: data.id,
                  status: data.status,
                  type: data.type,
                  date: data.date,
                  observations: data.observations,
                  place: data.place,
                  patient: data.patient,
                },
              ]);

              // BEGIN UPLOAD FILES
              if (files_.length > 0) {
                if (tokenDecode.id) {
                  const filesDB: IFilesUpload = {
                    patientId: "",
                    doctorId: "",
                    consultationId: data.id,
                    medicalHistoryId: "",
                    context: FileContext.CONSULTATION,
                    files: files_,
                  };

                  if (tokenDecode.role === RoleType.DOCTOR && values.patient) {
                    filesDB.patientId = values.patient;
                    filesDB.doctorId = tokenDecode.id;
                  }

                  if (tokenDecode.role === RoleType.PATIENT && values.doctor) {
                    filesDB.patientId = tokenDecode.id;
                    filesDB.doctorId = values.doctor;
                  }

                  if (filesDB.patientId.length === 0 || filesDB.doctorId.length === 0) {
                    return;
                  }

                  filesServices
                    .uploadFiles(filesDB)
                    .then((response: { statusCode: number; data: IConsultation }) => {
                      const { statusCode } = response;
                      if (statusCode === 201) {
                        Modal.info({
                          title: "Muy bien",
                          content: "Documentación adjunta",
                        });
                      }
                    })
                    .catch((errors) => {
                      const { error, message, statusCode } = errors;
                      if (statusCode === 409) {
                        Modal.warning({
                          content: errors_translate_es[message],
                        });
                      } else {
                        Modal.error({
                          content: `${message} (${statusCode} ${error})`,
                        });
                      }
                    });
                }
              }
              // END UPLOAD FILES
            }
          })
          .catch((errors) => {
            const { error, message, statusCode } = errors;
            if (statusCode === 409) {
              Modal.warning({
                content: errors_translate_es[message],
              });
            } else {
              Modal.error({
                content: `${message} (${statusCode} ${error})`,
              });
            }
          });
      }
    }
  };

  const normFile = (e: any) => {
    // "Upload event:", e);
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  // function onChangePatient(value: string) {
  //   // TODO: aqui se pudiera verificar el status del paciente o algo? o si tiene mas de 1 consulta,,
  //   // es un maximo de 2 en status PENDING
  // }
  function onChangeDoctor(value: string) {
    setSeletedDoctorId(value);
    formCreateConsultation.resetFields(["date", "hour"]);
  }

  const onChageConsultationsType = (e: any) => {
    setStateConsultationType(e.target.value);
  };

  const handleIsFamily = (e: any) => {
    setIsFamily(e.target.value);
  };

  return (
    <Form
      labelCol={{ span: 24 }}
      wrapperCol={{ span: 24 }}
      onFinish={onFinish}
      form={formCreateConsultation}
      initialValues={{ type: ConsultationsType.FACE_TO_FACE }}
    >
      <Row gutter={10}>
        {type === ConsultationsType.FACE_TO_FACE && (
          <>
            <Col lg={24}>
              <Form.Item name="type">
                <Radio.Group
                  options={[
                    { label: "Presencial", value: ConsultationsType.FACE_TO_FACE },
                    { label: "Escrita", value: ConsultationsType.WRITTEN },
                  ]}
                  onChange={onChageConsultationsType}
                  value={ConsultationsType.WRITTEN}
                  optionType="button"
                  buttonStyle="solid"
                />
              </Form.Item>
            </Col>
          </>
        )}

        {stateConsultationType === ConsultationsType.WRITTEN && (
          <Col lg={24}>
            <Alert
              message={
                <>
                  <div>
                    <Typography.Text strong>Fecha y hora:</Typography.Text>{" "}
                    {moment().format("D [de] MMMM [del] YYYY [a las] h:mm A")}.
                  </div>
                  <Typography.Text strong>Obligatorio para el doctor:</Typography.Text> La consulta escrita{" "}
                  <Typography.Text strong>
                    debe ser respondidá durante las próximas {process.env.REACT_APP_CONSULTATION_WRITTEN_HOURS} horas
                  </Typography.Text>
                  .
                </>
              }
              type="info"
            />
            <br />
          </Col>
        )}
        {tokenDecode.role === RoleType.PATIENT && (
          <>
            <Col lg={24}>
              <Form.Item name="isFamily" label={<strong>¿Es para un familiar?</strong>} initialValue={false}>
                <Radio.Group
                  onChange={handleIsFamily}
                  // value={value}
                >
                  <Space direction="horizontal">
                    <Radio value={true}>Si</Radio>
                    <Radio value={false}>No</Radio>
                  </Space>
                </Radio.Group>
              </Form.Item>
            </Col>
            {isFamily && (
              <Col lg={24}>
                <Form.Item
                  name="family"
                  label={<strong>Hijo</strong>}
                  rules={[{ required: true, message: "El hijo es requerido" }]}
                >
                  <Select
                    showSearch
                    placeholder="Busca un hijo"
                    optionFilterProp="children"
                    // onChange={onChangeDoctor}
                    // filterOption={(input, option) => option?.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                  >
                    {stateFamilyList?.map((family) => {
                      let info = `${family.firstName} ${family.lastName} ${family.email}`;
                      if (family.dni) info += " " + family.dni;

                      return (
                        <Select.Option key={family.id} value={family.id || ""}>
                          {info}
                        </Select.Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
            )}
            <Col lg={24}>
              <Form.Item
                name="doctor"
                label={<strong>Doctor</strong>}
                rules={[{ required: true, message: "El doctor es requerido" }]}
              >
                <Select
                  showSearch
                  placeholder="Busca un doctor"
                  optionFilterProp="children"
                  onChange={onChangeDoctor}
                 // filterOption={(input, option) => option?.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                >
                  {stateDoctorsList?.map((doctor) => {
                    let info = `${doctor.firstName} ${doctor.lastName} ${doctor.email}`;
                    if (doctor.dni) info += " " + doctor.dni;

                    return (
                      <Select.Option key={doctor.id} value={doctor.id || ""}>
                        {info}
                      </Select.Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
          </>
        )}
        {stateConsultationType !== ConsultationsType.WRITTEN && (
          <>
            {seletedDoctorId && (
              <FormItemDateHour
                doctorId={seletedDoctorId}
                formCreateConsultation={formCreateConsultation}
                consultationType={type}
              />
            )}

            {tokenDecode.role === RoleType.DOCTOR && (
              <Col lg={24}>
                <Form.Item
                  name="place"
                  label={<strong>Lugar</strong>}
                  rules={[
                    { required: true, message: "El lugar es requerido" },
                    { min: 5, message: "Minimo 5 caracteres" },
                    { max: 100, message: "Máximo 100 caracteres" },
                  ]}
                >
                  <Input maxLength={101} />
                </Form.Item>
              </Col>
            )}
          </>
        )}
      </Row>
      {tokenDecode.role === RoleType.DOCTOR && (
        <Form.Item
          name="patient"
          label={<strong>Paciente</strong>}
          extra={<>En caso de no encontrarlo asegurate de que haberlo añadido en el modulo de pacientes</>}
          rules={[{ required: true, message: "El paciente es requerido" }]}
        >
          <Select
            showSearch
            placeholder="Busca un paciente"
            optionFilterProp="children"
            // onChange={onChangePaciente}
            // filterOption={(input, option) => option?.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
          >
            {statePatientsList?.map((patient) => {
              let infoPatient = `${patient.firstName} ${patient.lastName} ${patient.email}`;
              if (patient.dni) infoPatient += " " + patient.dni;

              return (
                <Select.Option key={patient.id} value={patient.id}>
                  {infoPatient}
                </Select.Option>
              );
            })}
          </Select>
        </Form.Item>
      )}

      <Form.Item
        name="observations"
        label={<strong>Observaciones</strong>}
        rules={[
          { required: true, message: "Colocá alguna observación para saber el motivo de la consulta" },
          { min: 5, message: "Minimo 5 caracteres" },
          { max: 1000, message: "Máximo 1000 caracteres" },
        ]}
      >
        <Input.TextArea maxLength={1001} />
      </Form.Item>
      <Form.Item
        name="files"
        label={<strong>Imágenes o documentación</strong>}
        valuePropName="fileList"
        getValueFromEvent={normFile}
        extra="Máximo 5 MB. Solo Archivos pdf, jpg y png."
      >
        <Upload
          name="logo"
          // action="/upload.do"
          listType="picture"
          beforeUpload={() => {
            return false;
          }}
        >
          <Button icon={<UploadOutlined />}>Click para adjuntar</Button>
        </Upload>
      </Form.Item>
      <div style={{ textAlign: "center" }}>
        <Button type="primary" htmlType="submit">
          {type === ConsultationsType.SURGERY && "Crear Cirugía"}
          {type !== ConsultationsType.SURGERY && "Crear Consulta"}
        </Button>
      </div>
    </Form>
  );
};

export default FormCreateConsultation;
