import { Form, Input, Button, Row, Col, Modal, Spin } from "antd";
import { useState } from "react";
import { Link } from "react-router-dom";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";
import { errors_translate_es } from "../../../shared/errors/es";
import authServices from "../../../shared/services/auth.services";

const OlvidoClave = () => {
  const [stateSendMail, setStateSendMail] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);
  const [stateUser, setStateUser] = useState<{ firstName: string; email: string }>({
    firstName: "",
    email: "",
  });

  const onFinish = (values: { email: string }) => {
    setLoading(true);
    authServices
      .recoveryPasswordSendEmail({ email: values.email })
      .then(async (response: TypeResponse) => {
        const { statusCode, data } = response;

        if (statusCode === 200) {
          setStateSendMail(true);
          setLoading(false);
          setStateUser({ firstName: data.user.firstName, email: data.user.email });
        }
      })
      .catch((errors: TypeResponseError) => {
        setLoading(false);
        const { error, message, statusCode } = errors;
        if (statusCode === 409) {
          Modal.warning({
            title: "Hola",
            content: errors_translate_es[message],
          });
        } else {
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        }
      });
  };

  return (
    <Row justify="center" align="middle">
      <Col lg={7}>
        <br />
        <br />
        <div
          style={{
            border: "0.5px solid #00285f",
            borderRadius: 5,
            padding: 20,
          }}
        >
          {stateSendMail ? (
            <div style={{ textAlign: "center" }}>
              {stateUser.firstName} <strong>enviamos un email a {stateUser.email}</strong> con las instrucciones para restaurar tu
              clave
              <br />
              <br />
              <Link to="/auth/login">Volver al login</Link>
            </div>
          ) : (
            <Spin tip="Estamos validando tus datos" spinning={loading}>
              <Form
                labelCol={{ span: 24 }}
                wrapperCol={{ span: 24 }}
                initialValues={{ remember: true }}
                onFinish={onFinish}
              >
                <Form.Item
                  label="Email"
                  name="email"
                  rules={[
                    { required: true, message: "Escriba su email" },
                    { type: "email", message: "Escriba un email valido" },
                  ]}
                  extra={<span>Se le enviará un email con los detalles para recuperar su clave.</span>}
                >
                  <Input />
                </Form.Item>

                <div style={{ textAlign: "center" }}>
                  <Button type="primary" htmlType="submit">
                    Enviar Email
                  </Button>
                  <br />
                  <br />
                  <Link to="/auth/login">Volver al login</Link>
                </div>
              </Form>
            </Spin>
          )}
        </div>
      </Col>
    </Row>
  );
};

export default OlvidoClave;
