import { Tag } from "antd";
import { ConsultationsStatus } from "../../enum/consultations-status.enum";

type TypeConsultationsStatusTag = {
  status: ConsultationsStatus;
};

const ConsultationStatusTag = (props: TypeConsultationsStatusTag) => {
  const { status } = props;

  const buildTag = (): JSX.Element | null => {
    if (status === ConsultationsStatus.PENDING) {
      return (
        <Tag color="processing">
          <strong>Por atender</strong>{" "}
        </Tag>
      );
    } else if (status === ConsultationsStatus.PENDING_PAYMENT) {
      return (
        <Tag color="processing">
          <strong>Por pagar</strong>{" "}
        </Tag>
      );
    } else if (status === ConsultationsStatus.ATTENDED) {
      return <Tag color="default">Atendida</Tag>;
    } else if (status === ConsultationsStatus.CANCELLED_DOCTOR) {
      return <Tag color="error">Cancelada por el doctor</Tag>;
    } else if (status === ConsultationsStatus.CANCELLED_PATIENT) {
      return <Tag color="error">Cancelada por el paciente</Tag>;
    } else if (status === ConsultationsStatus.CANCELLED_ADMIN) {
      return <Tag color="error">Cancelada por el administrador</Tag>;
    }

    return null;
  };

  return buildTag();
};

export default ConsultationStatusTag;
