import { Layout, Menu, Popover, Button } from "antd";
import { Switch, Route, Link, useHistory } from "react-router-dom";
import { AimOutlined, ContainerOutlined } from "@ant-design/icons";
import ButtonSignOut from "../../../shared/components/ButtonSignOut";
import { Auth } from "../../../shared/utils/auth";
import { RoleType } from "../../../shared/enum/roletype.enum";
import ResultForbidden from "../../../shared/components/Results/ResultForbidden";
import CalendarList from "../Calendar/CalendarList";
import ResultNotFound from "../../../shared/components/Results/ResultNotFound";
import Attended from "../../../shared/components/ConsultationAttended";
import Family from "../Family";
import MedicalHistory from "../MedicalHistoryPreview";

const AppPatient = (): JSX.Element => {
  const history = useHistory();
  const tokenDecode = Auth.tokenDecode();
  if (!tokenDecode.validToken) {
    history.push("/auth/login");
    return <></>;
  }
  if (tokenDecode.role !== RoleType.PATIENT) return <ResultForbidden />;

  const isResponsive = window.matchMedia("screen and (max-width: 600px)").matches;

  const HeaderApp = (): JSX.Element => {
    return (
      <Layout.Header
        className="header"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          padding: "0 15px",
          backgroundColor: "#EFF2F5",
        }}
      >
        <div className="logo">
          <Link to="/doctor">
            <img
              src="https://www.tuhospitalvirtual.com/themes/tuagendamedica/resources/images/logos/logoweb.png"
              height="50"
              alt="THV"
            />
          </Link>
        </div>

        <Popover
          placement="bottomRight"
          title={
            <span>
              {tokenDecode.firstName} {tokenDecode.lastName} <br /> {tokenDecode.email}
            </span>
          }
          content={
            <div>
              <p>
                <Button type="primary" ghost onClick={() => history.push("/patient/family")}>
                  Hijos
                </Button>
              </p>
              <p>
                <ButtonSignOut />
              </p>
            </div>
          }
          trigger="click"
        >
          <Button
            style={{
              borderRadius: "100px",
              height: "50px",
              width: "50px",
            }}
          >
            {tokenDecode.firstName?.charAt(0)}
            {tokenDecode.lastName?.charAt(0)}
          </Button>
        </Popover>
      </Layout.Header>
    );
  };

  const ContentApp = (): JSX.Element => {
    return (
      <Layout.Content
        className="site-layout-background"
        style={{
          padding: 10,
          margin: 0,
          minHeight: 280,
          backgroundColor: "#FFFFFF",
        }}
      >
        <Switch>
          <Route exact path="/patient/calendar/list" component={CalendarList} />
          <Route exact path="/patient" component={CalendarList} />
          <Route exact path="/patient/attended" component={Attended} />
          <Route exact path="/patient/family" component={Family} />
          <Route exact path="/patient/medical-history" component={MedicalHistory} />
          <Route path="/patient" component={ResultNotFound} />
        </Switch>
      </Layout.Content>
    );
  };

  if (isResponsive) {
    return (
      <Layout style={{ height: "100vh" }}>
        <HeaderApp />
        <ContentApp />
        <Layout.Footer style={{ padding: 0, zIndex: 10 }}>
          <div style={{ display: "flex" }}>
            <Link to="/patient/calendar/list" style={{ width: "100%" }}>
              <Button size="small" style={{ height: 50, width: "100%" }}>
                <AimOutlined />
                <br />
                Consultas
              </Button>
            </Link>
          </div>
        </Layout.Footer>
      </Layout>
    );
  }

  return (
    <Layout style={{ height: "100vh" }}>
      <HeaderApp />
      <Layout>
        <Layout.Sider width={170} className="site-layout-background">
          <Menu
            mode="inline"
            defaultSelectedKeys={["1"]}
            defaultOpenKeys={["sub1"]}
            style={{ height: "100%", borderRight: 0 }}
          >
            <Menu.Item key="list" icon={<AimOutlined />}>
              <Link to="/patient/calendar/list">Consultas</Link>
            </Menu.Item>
            <Menu.Item key="/patient/medical-history" icon={<ContainerOutlined />}>
              <Link to="/patient/medical-history">Hístorial clínico</Link>
            </Menu.Item>
          </Menu>
        </Layout.Sider>

        <ContentApp />
      </Layout>
    </Layout>
  );
};

export default AppPatient;
