import { useEffect, useState } from "react";
import { List, Row, Col, Input, Button, Modal } from "antd";
import { errors_translate_es } from "../../../shared/errors/es";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import inventoryServices from "../../../shared/services/inventory.services";
import { Auth } from "../../../shared/utils/auth";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";
import { IStock } from "../../../shared/interfaces/inventory.interfaces";
import FormCreateStock from "./FormCreateStock";
import ManageProduct from "./ManageProduct";

let stateListStatic: IStock[] = [];

const Inventory = () => {
  const tokenDecode = Auth.tokenDecode();

  const [stateList, setStateList] = useState<IStock[]>([]);
  const [stateSelected, setStateSelected] = useState<IStock>();

  useEffect(() => {
    if (tokenDecode.id) {
      inventoryServices
        .getInventory(tokenDecode.id)
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            setStateList(data);
            setStateListStatic(data);
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;
          if (statusCode === 409) {
            Modal.warning({
              content: errors_translate_es[message],
            });
          } else {
            Modal.error({
              title: "Hola",
              content: `${message} (${statusCode} ${error})`,
            });
          }
        });
    }
  }, [tokenDecode.id]);

  const setStateListStatic = (list: IStock[]): void => {
    stateListStatic = list;
  };

  const onSearch = (value: string) => {
    setStateList(
      stateListStatic.filter(
        (inventory) =>
          inventory.name.toLowerCase().includes(value.toLowerCase()) ||
          inventory.sku.toLowerCase().includes(value.toLowerCase()) ||
          inventory.stock.toString().includes(value)
      )
    );
  };

  const [modalCrear, setModalCrear] = useState<IModal>({
    visible: false,
  });

  return (
    <>
      <Button type="primary" onClick={() => setModalCrear({ ...modalCrear, visible: true })}>
        Agregar al inventario
      </Button>
      <div style={{ height: 10 }}></div>
      <Row gutter={[10, 10]}>
        <Col
          lg={15}
          style={{
            backgroundColor: "#FFFFFF",
            height: "85vh",
            overflow: "scroll",
          }}
        >
          <Input.Search
            placeholder=""
            // onSearch={onSearch}
            // enterButton
            onChange={(e) => {
              onSearch(e.target.value);
            }}
            style={{
              marginTop: 5,
            }}
          />
          <br />
          <br />
          <List
            itemLayout="horizontal"
            dataSource={stateList}
            renderItem={(inventory) => {
              return (
                <List.Item
                  style={{
                    backgroundColor: inventory.id === stateSelected?.id ? "#EDEBE8" : "",
                    paddingLeft: 5,
                    cursor: "pointer",
                  }}
                  onClick={() => {
                    setStateSelected(inventory);
                  }}
                >
                  <List.Item.Meta
                    title={`${inventory.name}`}
                    description={`SKU: ${inventory.sku}. STOCK: ${inventory.stock}.`}
                  />
                </List.Item>
              );
            }}
          />
        </Col>
        <Col
          lg={9}
          style={{
            backgroundColor: "#FFFFFF",
            height: "85vh",
            overflow: "scroll",
          }}
        >
          {stateSelected && (
            <ManageProduct
              product={stateSelected}
              setStateList={setStateList}
              stateListStatic={stateListStatic}
              setStateListStatic={setStateListStatic}
              setStateSelected={setStateSelected}
            />
          )}
        </Col>
      </Row>

      <FormCreateStock
        modalCrear={modalCrear}
        setModalCrear={setModalCrear}
        setStateList={setStateList}
        stateListStatic={stateListStatic}
      />
    </>
  );
};

export default Inventory;
