import moment, { Moment } from "moment";
import { DayCodeMoment } from "../enum/day.enum";
import { IScheduleDay } from "../interfaces/doctor.interfaces";

// Dias feriados por ejemplo - Se deben renovar anualmente
export const disabledDates = ["2021-09-21", "2021-09-29"];

export function buildDisabledDaysDoctorSchedule(doctorSchedule: IScheduleDay[]): number[] {
  const disabledDaysDoctorSchedule: number[] = [];
  doctorSchedule.forEach((day) => {
    if (!day.active) {
      disabledDaysDoctorSchedule.push(DayCodeMoment[day.day]);
    }
  });

  return disabledDaysDoctorSchedule;
}

function range(start: number, end: number) {
  const result = [];
  for (let i = start; i <= end; i++) {
    result.push(i);
  }
  return result;
}

export function buildDisabledHoursByDay(
  doctorSchedule: IScheduleDay[],
  dayCode: DayCodeMoment,
  date: Moment
): number[] {
  const day_ = doctorSchedule.find((dayConfig) => DayCodeMoment[dayConfig.day] === dayCode);

  if (!day_) {
    return [0];
  }

  const from = Number(day_.from.split(":")[0]);
  const until = Number(day_.until.split(":")[0]);

  const rangeFrom = range(0, 23).splice(0, from);
  const rangeUntil = range(0, 23).splice(until + 1, 23);

  let rangeFromNow: number[] = [];
  if (moment(date.format("YYYY-MM-DD")).isSame(moment().format("YYYY-MM-DD"))) {
    const fromNow = moment().hours();
    rangeFromNow = range(0, 23).splice(0, fromNow);
  }

  return [...rangeFrom, ...rangeUntil, ...rangeFromNow];
}

export function buildDisabledMinutesByDay(
  doctorSchedule: IScheduleDay[],
  dayCode: DayCodeMoment,
  date: Moment
): number[] {
  const day_ = doctorSchedule.find((dayConfig) => DayCodeMoment[dayConfig.day] === dayCode);

  if (!day_) {
    return [0];
  }

  // const fromNow = moment().add(5, "minutes").minutes();
  const until = Number(day_.until.split(":")[1]);

  // const rangeFrom = range(0, 59).splice(0, fromNow);
  const rangeUntil = range(0, 59).splice(until + 1, 59);

  let rangeFromNow: number[] = [];
  if (moment(date.format("YYYY-MM-DD")).isSame(moment().format("YYYY-MM-DD"))) {
    const fromNow = moment().add(5, "minutes").minutes();
    rangeFromNow = range(0, 59).splice(0, fromNow);
  }

  return rangeFromNow.concat(rangeUntil);
}

export function getDetailsScheduleDay(doctorSchedule: IScheduleDay[], dayCode: DayCodeMoment): IScheduleDay | false {
  return doctorSchedule.find((dayDetail) => DayCodeMoment[dayDetail.day] === dayCode) || false;
}
