import { useEffect, useState } from "react";
import { List, Row, Col, Input, Button, Modal } from "antd";
import "moment/locale/es";
import doctorServices from "../../../shared/services/doctor.services";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import { GenderType } from "../../../shared/enum/gender.enum";
import moment from "moment";
import FormCreateDoctor from "./FormCreateDoctor";
import { IUser } from "../../../shared/interfaces/user.interfaces";
import FormUpdateDoctor from "./FormUpdateDoctor";
import { ICreateDoctor } from "../../../shared/interfaces/doctor.interfaces";

let stateDoctorsListStatic: (IUser & { doctorDetail: ICreateDoctor })[] = [];

const Doctors = () => {
  const [stateDoctorsList, setStateDoctorsList] = useState<(IUser & { doctorDetail: ICreateDoctor })[]>([]);
  const [stateDoctorSelected, setStateDoctorSelected] = useState<IUser & { doctorDetail: ICreateDoctor }>();

  useEffect(() => {
    doctorServices
      .getDoctors()
      .then((response: TypeResponse) => {
        const { statusCode, data } = response;
        if (statusCode === 200) {
          setStateDoctorsList(data);
          stateDoctorsListStatic = data;
        }
      })
      .catch((errors: TypeResponseError) => {
        const { error, message, statusCode } = errors;

        Modal.error({
          title: "Hola",
          content: `${message} (${statusCode} ${error})`,
        });
      });
  }, []);

  const onSearch = (value: string) => {
    setStateDoctorsList(
      stateDoctorsListStatic.filter(
        (doctor) =>
          doctor.firstName.toLowerCase().includes(value.toLowerCase()) ||
          doctor.lastName.toLowerCase().includes(value.toLowerCase()) ||
          doctor.email.toLowerCase().includes(value.toLowerCase()) ||
          doctor.dni?.toLowerCase().includes(value.toLowerCase())
      )
    );
  };

  const [modalCrear, setModalCrear] = useState<IModal>({
    visible: false,
  });
  const [modalUpdate, setModalUpdate] = useState<IModal>({
    visible: false,
  });

  return (
    <>
      <Button type="primary" onClick={() => setModalCrear({ ...modalCrear, visible: true })}>
        Crear doctor
      </Button>
      <div style={{ height: 10 }}></div>
      <Row gutter={[10, 10]}>
        <Col
          lg={12}
          style={{
            backgroundColor: "#FFFFFF",
            height: "85vh",
            overflow: "scroll",
          }}
        >
          <Input.Search
            placeholder=""
            onChange={(e) => {
              onSearch(e.target.value);
            }}
            style={{
              marginTop: 5,
            }}
          />
          <br />
          <br />
          <List
            itemLayout="horizontal"
            dataSource={stateDoctorsList}
            renderItem={(doctor) => {
              const age = moment().diff(doctor.birthDate, "years");
              const gender = doctor.gender === GenderType.FELAME ? "Femenino" : "Masculino";
              return (
                <List.Item
                  style={{
                    backgroundColor: doctor.id === stateDoctorSelected?.id ? "#EDEBE8" : "",
                    paddingLeft: 5,
                    cursor: "pointer",
                  }}
                  onClick={() => {
                    setStateDoctorSelected(doctor);
                    setModalUpdate({ visible: true });
                  }}
                >
                  <List.Item.Meta
                    // avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />}
                    title={`${doctor.firstName} ${doctor.lastName}`}
                    description={`${age} años. ${gender}`}
                  />
                </List.Item>
              );
            }}
          />
        </Col>
        <Col
          lg={12}
          style={{
            backgroundColor: "#FFFFFF",
            height: "85vh",
            overflow: "scroll",
          }}
        >
          {/* ? */}
        </Col>
      </Row>

      <FormCreateDoctor
        modalCrear={modalCrear}
        setModalCrear={setModalCrear}
        setStateDoctorsList={setStateDoctorsList}
        stateDoctorsListStatic={stateDoctorsListStatic}
      />

      {stateDoctorSelected && (
        <FormUpdateDoctor
          modalCrear={modalUpdate}
          setModalCrear={setModalUpdate}
          setStateDoctorsList={setStateDoctorsList}
          stateDoctorsListStatic={stateDoctorsListStatic}
          setStateDoctorSelected={setStateDoctorSelected}
          stateDoctorSelected={stateDoctorSelected}
        />
      )}
    </>
  );
};

export default Doctors;
