import { Button, Col, Form, Input, Modal, Row, Space, Upload } from "antd";
import { UploadOutlined } from "@ant-design/icons";
import { IHistory, IMedicalHistory } from "../../interfaces/medical-history.interfaces";
import { IModal } from "../../interfaces/modals.interfaces";
import medicalHistoryServices from "../../services/medical-history.services";
import { FileContext, FileExtension } from "../../enum/files.enum";
import { IFile, IFilesUpload } from "../../interfaces/files.interfaces";
import { getBase64 } from "../../utils/getBase64";
import filesServices from "../../services/files.services";
import { TypeResponseError } from "../../types/response.type";
import { RoleType } from "../../enum/roletype.enum";
import { Auth } from "../../utils/auth";

interface IMedicalHistoryCreate {
  idPatient: string;
  visible: boolean;
  setModalCrearHistory: any;
  setStateHistory: any;
}

interface IMedicalHistoryForm {
  bloodType: string;
  weight: string;
  height: string;
  smoke: string;
  alcoholicDrinks: string;
  drugs: string;
  allergies: string;
  surgeries: string;
  pathologicalAntecedents: string;
  familyBackground: string;
  files: { originFileObj: any; type: "image/png" | "image/jpeg" | "application/pdf" }[];
}

const MedicalHistoryCreate = (props: IMedicalHistoryCreate) => {
  const tokenDecode = Auth.tokenDecode();

  const { idPatient, visible, setModalCrearHistory, setStateHistory } = props;
  const [form] = Form.useForm();

  const onFinishHistory = async (values: IMedicalHistoryForm) => {
    const files_: IFile[] = [];
    if (values.files) {
      for (const file of values.files) {
        let extension = FileExtension.PNG;
        if (file.type === "image/png") {
          extension = FileExtension.PNG;
        } else if (file.type === "image/jpeg") {
          extension = FileExtension.JPEG;
        } else if (file.type === "application/pdf") {
          extension = FileExtension.PDF;
        }

        let base64 = await getBase64(file.originFileObj);
        base64 = base64.split(";base64,").pop();

        files_.push({
          extension,
          base64,
        });
      }
    }

    let history: IHistory[] = [];

    if (values.bloodType) {
      history.push({
        title: "Grupo Sanguíneo",
        observations: values.bloodType,
      });
    }

    if (values.weight) {
      history.push({
        title: "Peso",
        observations: values.weight,
      });
    }

    if (values.height) {
      history.push({
        title: "Altura",
        observations: values.height,
      });
    }

    if (values.smoke) {
      history.push({
        title: "Fuma",
        observations: values.smoke,
      });
    }

    if (values.alcoholicDrinks) {
      history.push({
        title: "Bebidas alcohólicas",
        observations: values.alcoholicDrinks,
      });
    }

    if (values.drugs) {
      history.push({
        title: "Drogas",
        observations: values.drugs,
      });
    }

    if (values.allergies) {
      history.push({
        title: "Alergias",
        observations: values.allergies,
      });
    }

    if (values.pathologicalAntecedents) {
      history.push({
        title: "Antecedentes patológicos",
        observations: values.pathologicalAntecedents,
      });
    }

    if (values.familyBackground) {
      history.push({
        title: "Antecedentes familiares",
        observations: values.familyBackground,
      });
    }

    if (values.surgeries) {
      history.push({
        title: "Cirugías",
        observations: values.surgeries,
      });
    }

    if (history.length < 1) {
      setModalCrearHistory((state: IModal) => ({ ...state, visible: false }));
      return;
    }

    if (!idPatient || idPatient.length < 1) {
      Modal.error({
        title: "Hola",
        content: `idPatient not found`,
      });
      return;
    }

    if (tokenDecode.id) {
      medicalHistoryServices
        .create({
          patientId: idPatient,
          doctorId: tokenDecode.id,
          history,
        })
        .then(async (response: { statusCode: number; data: IMedicalHistory }) => {
          const { statusCode, data } = response;
          if (statusCode === 201) {
            form.resetFields();

            Modal.info({
              title: "Muy bien",
              content: "Se ha asignado un historial clínico al paciente",
            });

            await setStateHistory((state: IMedicalHistory[]): IMedicalHistory[] => [
              {
                id: data.id,
                doctorId: data.doctorId,
                createdAt: data.createdAt,
                history: data.history,
              },
              ...state,
            ]);

            setModalCrearHistory((state: IModal) => ({ ...state, visible: false }));

            // BEGIN UPLOAD FILES
            if (files_.length > 0) {
              if (tokenDecode.id) {
                const filesDB: IFilesUpload = {
                  patientId: "",
                  doctorId: "",
                  consultationId: "",
                  medicalHistoryId: data.id,
                  context: FileContext.MEDICAL_HISTORY,
                  files: files_,
                };

                if (tokenDecode.role === RoleType.DOCTOR && idPatient) {
                  filesDB.patientId = idPatient;
                  filesDB.doctorId = tokenDecode.id;
                }

                // en este caso no aplica porque solo los doctores puedes actualizar el medical history de los pacientes
                // if (tokenDecode.role === RoleType.PATIENT && values.doctor) {
                //   filesDB.patientId = tokenDecode.id;
                //   filesDB.doctorId = values.doctor;
                // }

                if (filesDB.patientId.length === 0 || filesDB.doctorId.length === 0) {
                  return;
                }

                filesServices
                  .uploadFiles(filesDB)
                  .then(async (response: { statusCode: number; data: any }) => {
                    const { statusCode, data } = response;
                    if (statusCode === 201) {
                      Modal.info({
                        title: "Muy bien",
                        content: "Documentación adjunta",
                      });

                      await setStateHistory((state: IMedicalHistory[]): IMedicalHistory[] => {
                        const listHistories = state.map((history) => {
                          if (history.id === data.medicalHistoryId) {
                            history.files = data.files;
                          }
                          return history;
                        });

                        return listHistories;
                      });
                    }
                  })
                  .catch((errors: TypeResponseError) => {
                    const { error, message, statusCode } = errors;
                    Modal.error({
                      content: `${message} (${statusCode} ${error})`,
                    });
                  });
              }
            }
            // END UPLOAD FILES
          }
        })
        .catch((errors) => {
          const { error, message, statusCode } = errors;

          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
    }
  };

  const normFile = (e: any) => {
    // "Upload event:", e);
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  return (
    <Modal
      title="Actualizar Historial clínico"
      visible={visible}
      onCancel={() => setModalCrearHistory((state: any) => ({ ...state, visible: false }))}
      footer={false}
    >
      <Form
        form={form}
        labelCol={{ span: 24 }}
        wrapperCol={{ span: 24 }}
        initialValues={{ remember: true }}
        onFinish={onFinishHistory}
      >
        <Row gutter={[10, 10]}>
          <Col lg={8}>
            <Form.Item label={<strong>Grupo Sanguíneo</strong>} name="bloodType">
              <Input />
            </Form.Item>
          </Col>
          <Col lg={8}>
            <Form.Item label={<strong>Peso</strong>} name="weight">
              <Input />
            </Form.Item>
          </Col>
          <Col lg={8}>
            <Form.Item label={<strong>Estatura</strong>} name="height">
              <Input />
            </Form.Item>
          </Col>
          <Col lg={8}>
            <Form.Item label={<strong>Fuma</strong>} name="smoke">
              <Input.TextArea />
            </Form.Item>
          </Col>
          <Col lg={8}>
            <Form.Item label={<strong>Bebidas alcohólicas</strong>} name="alcoholicDrinks">
              <Input.TextArea />
            </Form.Item>
          </Col>
          <Col lg={8}>
            <Form.Item label={<strong>Drogas</strong>} name="drugs">
              <Input.TextArea />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item label={<strong>Alergias</strong>} name="allergies">
              <Input.TextArea />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item label={<strong>Cirugías</strong>} name="surgeries">
              <Input.TextArea />
            </Form.Item>
          </Col>
          <Col lg={24}>
            <Form.Item label={<strong>Antecedentes patologicos</strong>} name="pathologicalAntecedents">
              <Input.TextArea />
            </Form.Item>
            <Form.Item label={<strong>Antecedentes Familiares</strong>} name="familyBackground">
              <Input.TextArea />
            </Form.Item>
          </Col>

          <Col lg={24}>
            <Form.Item
              name="files"
              label={<strong>Imágenes o documentación</strong>}
              valuePropName="fileList"
              getValueFromEvent={normFile}
              extra="Máximo 5 MB. Solo Archivos pdf, jpg y png."
            >
              <Upload
                name="logo"
                listType="picture"
                beforeUpload={() => {
                  return false;
                }}
              >
                <Button icon={<UploadOutlined />}>Click para adjuntar</Button>
              </Upload>
            </Form.Item>
          </Col>

          <Col lg={24} style={{ textAlign: "center" }}>
            <Space>
              <Button type="primary" htmlType="submit">
                Actualizar Historial Clínico
              </Button>
            </Space>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

export default MedicalHistoryCreate;
