import { Button, Col, DatePicker, Form, Input, Modal, Row, Select } from "antd";
import { RoleType, RoleTypeEmail, RoleTypePassword } from "../../../shared/enum/roletype.enum";
import { UserStatusEnum } from "../../../shared/enum/user-status.enum";
import { errors_translate_es } from "../../../shared/errors/es";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import { IPatient } from "../../../shared/interfaces/patients.interfaces";
import { IUser } from "../../../shared/interfaces/user.interfaces";
import authServices from "../../../shared/services/auth.services";
import locale from "antd/es/date-picker/locale/es_ES";
import { FORMAT_DATE_DD_MM_YYY } from "../../../shared/utils/formats";
import { Auth } from "../../../shared/utils/auth";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";

interface IFormCreatePatient {
  modalCrear: IModal;
  setModalCrear: any;
  setStatePatientsList: any;
  statePatientsListStatic: IPatient[];
}

const FormCreatePatient = (props: IFormCreatePatient) => {
  const tokenDecode = Auth.tokenDecode();

  const { modalCrear, setModalCrear, setStatePatientsList, statePatientsListStatic } = props;
  const [formCreatePatient] = Form.useForm();

  const onFinish = (values: IUser) => {
    if (tokenDecode.id) {
      authServices
        .signup({
          firstName: values.firstName,
          lastName: values.lastName,
          email: `${values.dni}${RoleTypeEmail.PATIENT_FAMILY}`,
          dni: values.dni,
          password: RoleTypePassword.PATIENT,
          role: RoleType.PATIENT,
          gender: values.gender,
          birthDate: values.birthDate,
          familyId: tokenDecode.id,
          medicalCenterId: "TODO: definir here created by PATIEN",
        })
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          if (statusCode === 201) {
            formCreatePatient.resetFields();
            setModalCrear({ visible: false });

            let status = UserStatusEnum.ACTIVE;
            if (data.status === UserStatusEnum.ACTIVE) {
              status = UserStatusEnum.ACTIVE;
            } else if (data.status === UserStatusEnum.BLOCKED) {
              status = UserStatusEnum.BLOCKED;
            }

            setStatePatientsList((state: IPatient[]) => [
              ...state,
              {
                id: data.id,
                firstName: data.firstName,
                lastName: data.lastName,
                email: data.email,
                dni: data.dni,
                status,
                gender: data.gender,
                birthDate: data.birthDate,
                familyId: data.familyId,
              },
            ]);

            statePatientsListStatic.push({
              id: data.id,
              firstName: data.firstName,
              lastName: data.lastName,
              email: data.email,
              dni: data.dni,
              status,
              gender: data.gender,
              birthDate: data.birthDate,
              familyId: data.familyId,
            });
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;
          if (statusCode === 409) {
            Modal.warning({
              content: errors_translate_es[message],
            });
          } else {
            Modal.error({
              title: "Hola",
              content: `${message} (${statusCode} ${error})`,
            });
          }
        });
    }
  };

  return (
    <Modal
      title="Agregar hijo"
      visible={modalCrear.visible}
      onCancel={() => setModalCrear({ ...modalCrear, visible: false })}
      footer={false}
    >
      <Form
        labelCol={{ span: 24 }}
        wrapperCol={{ span: 24 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        form={formCreatePatient}
      >
        <Row gutter={[10, 10]}>
          <Col lg={12}>
            <Form.Item name="gender" label="Genero" rules={[{ required: true, message: "Selecciona el genero" }]}>
              <Select placeholder="Seleccione" allowClear>
                <Select.Option value="MALE">Masculino</Select.Option>
                <Select.Option value="FEMALE">Femenino</Select.Option>
              </Select>
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item
              name="birthDate"
              label="Fecha de nacimiento"
              rules={[{ required: true, message: "La fecha de nacimimiento es requerida" }]}
            >
              <DatePicker format={FORMAT_DATE_DD_MM_YYY} locale={locale} style={{ width: "100%" }} />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item label="Nombre" name="firstName" rules={[{ required: true, message: "Nombre es requerido" }]}>
              <Input />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item label="Apellido" name="lastName" rules={[{ required: true, message: "Apellido es requerido" }]}>
              <Input />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item label="Cédula" name="dni" rules={[{ required: true, message: "La cédula es requerida" }]}>
              <Input />
            </Form.Item>
          </Col>

          <Col lg={24} style={{ textAlign: "center" }}>
            <Button type="primary" htmlType="submit">
              Agregar Hijo
            </Button>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

export default FormCreatePatient;
