import axios from "axios";

const instanceApiConsultations = axios.create({
  baseURL: process.env.REACT_APP_CONSULTATIONS_API_BASE_URL,
});

console.log(`b`, process.env.REACT_APP_CONSULTATIONS_API_BASE_URL);

instanceApiConsultations.interceptors.request.use(
  function (config) {
    return config;
  },
  function (error) {
    return Promise.reject(error);
  }
);

instanceApiConsultations.interceptors.response.use(
  function (response): any {
    // Any status code that lie within the range of 2xx cause this function to trigger
    // Do something with response data

    return {
      data: response.data,
      statusCode: response.status,
    };
  },
  function (error) {
    // Any status codes that falls outside the range of 2xx cause this function to trigger
    // Do something with response error
    if (error.message.includes("Network Error")) {
      return Promise.reject({
        message: "Network not connected. Verify your internet connexion.",
        error: "Network Error",
        statusCode: 0,
      });
    }
    return Promise.reject(JSON.parse(error.request.responseText));
  }
);

// interface IOptionsRequest {
//   method: string;
//   url: string;
//   data: Object;
//   private?: boolean;
// }

const requestApiConsultations = (options: any) => {
  if (options.private) {
    options.headers = { Authorization: `Bearer ${window.sessionStorage.getItem("token")}` };
  }

  return instanceApiConsultations(options);
};

export default requestApiConsultations;
