import { useLocation } from "react-router-dom";
import { IConsultation } from "../../interfaces/consultations.interfaces";
import Preview from "../../../apps/doctor/Consultations/Preview";

const Attended = () => {
  const { state } = useLocation();
  if (!state) {
    return <>State not send. You must send it obligatorily</>;
  }

  const state_: any = state;
  if (!state_.consultation) {
    return <>Consultation not send. You must send it obligatorily</>;
  }

  const consultation: IConsultation = state_.consultation;

  return <Preview consultation={consultation} />;
};

export default Attended;
