import jwt_decode from "jwt-decode";
import { GenderType } from "../enum/gender.enum";
import { RoleType } from "../enum/roletype.enum";
import { UserStatusEnum } from "../enum/user-status.enum";

export type TypeTokenDecode = {
  validToken: boolean;
  id?: string;
  firstName?: string;
  lastName?: string;
  email?: string;
  status?: UserStatusEnum;
  role?: RoleType;
  dni?: string;
  gender?: GenderType;
  birthDate?: Date;
  familyId?: string;
};

export class Auth {
  static readonly signOut = () => {
    window.sessionStorage.setItem("token", "");
    return 1;
  };

  static readonly tokenDecode = (): TypeTokenDecode => {
    try {
      const token = window.sessionStorage.getItem("token") || "";
      if (!token || token === undefined || token.length < 1) {
        return { validToken: false };
      }
      const tokenDecode: TypeTokenDecode = jwt_decode(token);
      return {
        ...tokenDecode,
        validToken: true,
      };
    } catch (error) {
      return { validToken: false };
    }
  };
}
