import requestApiConsultations from "../../config/requestApiConsultations";

interface PatientServices {
  getList(): Promise<any>;
  getMedicalHistory(patientId: string): Promise<any>;
  getListFamily(patientId: string): Promise<any>;
}

const patientsServices: PatientServices = {
  getList: function () {
    return requestApiConsultations({
      method: "GET",
      url: "/patients",
      private: true,
    });
  },
  getMedicalHistory: function (patientId) {
    return requestApiConsultations({
      method: "GET",
      url: `/patients/${patientId}/medical-history`,
      private: true,
    });
  },
  getListFamily: function (patientId) {
    return requestApiConsultations({
      method: "GET",
      url: `/patients/${patientId}/family`,
      private: true,
    });
  },
};

export default patientsServices;
