import { Button, Form, Input, Modal } from "antd";
import { IConsultation } from "../../../shared/interfaces/consultations.interfaces";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import consultationsServices from "../../../shared/services/consultations.services";
import { Auth } from "../../../shared/utils/auth";

type TypeModalCancel = {
  consultation: IConsultation;
  setStateSelected: any;
  modalCancel: IModal;
  setModalCancel: any;
  setStateListConsultations: any;
};

type TypeFormCancel = {
  observation: string;
};

const ModalCancel = (props: TypeModalCancel) => {
  const tokenDecode = Auth.tokenDecode();
  const [formCancel] = Form.useForm();
  const { consultation, setStateSelected, modalCancel, setModalCancel, setStateListConsultations } = props;

  const onFinishCancel = (values: TypeFormCancel) => {
    if (tokenDecode.role) {
      consultationsServices
        .cancelConsultation(consultation.id, { observations: [`${values.observation}`] }, tokenDecode.role)
        .then((response: any) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            formCancel.resetFields();
            Modal.success({
              title: "Hola",
              content: "Consulta cancelada",
            });
            setModalCancel({ visible: false });
            setStateListConsultations((consultations: IConsultation[]) => {
              const updateConsultations = consultations.map((consultation) => {
                if (consultation.id === data.id) {
                  consultation.status = data.status;
                  consultation.observations = data.observations;
                }
                return consultation;
              });

              return updateConsultations;
            });
          }
        })
        .catch((errors) => {
          const { error, message, statusCode } = errors;
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
    }
  };

  return (
    <Modal
      title={<span>¿Por qué cancelas la consulta?</span>}
      visible={modalCancel.visible}
      onCancel={() => {
        setModalCancel({ visible: false });
        setStateSelected(null);
      }}
      centered
      footer={false}
    >
      <Form onFinish={onFinishCancel} form={formCancel}>
        <Form.Item
          name="observation"
          rules={[
            { required: true, message: "Colocá el motivo de la cancelación" },
            { min: 5, message: "Minimo 5 caracteres" },
            { max: 100, message: "Máximo 100 caracteres" },
          ]}
        >
          <Input autoFocus maxLength={100} />
        </Form.Item>
        <br />
        <div style={{ textAlign: "center" }}>
          <Button type="primary" danger htmlType="submit">
            Cancelar consulta
          </Button>
        </div>
      </Form>
    </Modal>
  );
};

export default ModalCancel;
