import { IScheduleDay } from "../../../shared/interfaces/doctor.interfaces";

interface IBtnSchedule {
  doctorSchedule: IScheduleDay[];
}

const BtnSchedule = (props: IBtnSchedule) => {
  const { doctorSchedule } = props;
  return (
    <>
      {doctorSchedule.map(
        (detailDay) =>
          detailDay.active && (
            <div key={detailDay.day} style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <strong>
                {detailDay.day.charAt(0)}
                {detailDay.day.slice(1, 3).toLowerCase()}:
              </strong>{" "}
              &nbsp;
              <span>
                {detailDay.from} a {detailDay.until}
              </span>
            </div>
          )
      )}
    </>
  );
};

export default BtnSchedule;
