import { Typography } from "antd";
import moment from "moment";
import { ConsultationsStatus } from "../../enum/consultations-status.enum";
import { GenderType } from "../../enum/gender.enum";
import { IConsultation } from "../../interfaces/consultations.interfaces";
import ConsultationStatusTag from "../ConsultationStatusTags";

const ConsultationInfo = (props: { consultation: IConsultation }) => {
  const { consultation } = props;

  const date = (
    <div>
      <Typography.Text strong>Fecha:</Typography.Text>{" "}
      {moment(consultation.date).utc(false).format("D [de] MMMM [del] YYYY")}
    </div>
  );
  const hour = (
    <div>
      <Typography.Text strong>Hora:</Typography.Text> {moment(consultation.date).utc(false).format("h:mm A")}
    </div>
  );
  const name = (
    <div>
      <Typography.Text strong>Paciente:</Typography.Text> {consultation?.patient?.firstName}{" "}
      {consultation?.patient?.lastName}
    </div>
  );

  const age = (
    <div>
      <Typography.Text strong>Edad:</Typography.Text> {moment().diff(consultation?.patient?.birthDate, "years")} años.
    </div>
  );

  const gender = (
    <div>
      <Typography.Text strong>Genero:</Typography.Text>{" "}
      {consultation?.patient?.gender === GenderType.FELAME ? "Femenino" : "Masculino"}
    </div>
  );

  const place = (
    <>
      {consultation.place && consultation.place.trim().length > 5 ? (
        <div>
          <Typography.Text strong>Lugar:</Typography.Text> {consultation?.place}
        </div>
      ) : null}
    </>
  );

  const observations = () => {
    return (
      <div>
        <Typography.Text strong>Observaciones:</Typography.Text>
        <div
          dangerouslySetInnerHTML={{
            __html: consultation.observations[0],
          }}
        ></div>
      </div>
    );
  };

  // TODO: add lugar de atencion ,,, si es type ONLINE, Lugar: Online
  // TODO: add email,, es necesario mostrar el email del paiente? quizas para confirmarlo con el paciente ? no se
  // TODO: add telefonos.,,, mmm,, puede ser que necesite llamar a los pacientes desde el calendar para informarles algo? ragenda o cancel? puede ser

  return (
    <>
      <ConsultationStatusTag status={consultation.status} />
      {date}
      {hour}
      {name}
      {age}
      {gender}
      {place}
      {(consultation.status === ConsultationsStatus.PENDING ||
        consultation.status === ConsultationsStatus.PENDING_PAYMENT ||
        consultation.status === ConsultationsStatus.CANCELLED_PATIENT ||
        consultation.status === ConsultationsStatus.CANCELLED_DOCTOR ||
        consultation.status === ConsultationsStatus.CANCELLED_ADMIN) &&
        observations()}
    </>
  );
};

export default ConsultationInfo;
