import { useHistory } from "react-router-dom";
import { Button } from "antd";
import { Auth } from "../../utils/auth";

const ButtonSignOut = () => {
  const history = useHistory();

  return (
    <Button
      type="dashed"
      danger
      onClick={() => {
        if (Auth.signOut()) {
          history.push("/auth/login");
        }
      }}
      // style={{
      //   padding: 0,
      // }}
    >
      Cerrar Sesión
    </Button>
  );
};

export default ButtonSignOut;
