import { Alert, Button, Col, Form, Input, InputNumber, Modal, Popconfirm, Row, Typography } from "antd";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { useEffect } from "react";
import { IStock } from "../../../shared/interfaces/inventory.interfaces";
import { LABEL_COL_AND_WRAPPER_COL } from "../../../shared/utils/formats";
import inventoryServices from "../../../shared/services/inventory.services";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";

type TypeManageProduct = {
  product: IStock;
  setStateList: any;
  stateListStatic: IStock[];
  setStateListStatic: (list: IStock[]) => void;
  setStateSelected: any;
};
const ManageProduct = (props: TypeManageProduct) => {
  const { product, setStateList, stateListStatic, setStateListStatic, setStateSelected } = props;
  const [formUpdate] = Form.useForm();
  const [formDelete] = Form.useForm();

  useEffect(() => {
    formUpdate.setFieldsValue({ stock: product.stock });
    formDelete.setFieldsValue({ id: product.id });
  }, [formUpdate, formDelete, product]);

  const onFinishUpdate = function (values: { stock: number }) {
    if (product.id) {
      inventoryServices
        .updateInventory(product.id, { stock: values.stock })
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            // setStateSelected(null);
            Modal.success({
              title: "Hola",
              content: `Actualizaste el stock exitosamente`,
            });

            setStateList((state: IStock[]) => {
              const udpdateList = stateListStatic.map((product) => {
                if (product.id === data.id) {
                  product.stock = data.stock;
                }
                return product;
              });
              setStateListStatic(udpdateList);
              return udpdateList;
            });
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;

          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
    }
  };

  const onFinishDelete = function (values: { id: string }) {
    if (product.id) {
      inventoryServices
        .deleteInventory(product.id)
        .then((response: TypeResponse) => {
          const { statusCode } = response;
          if (statusCode === 200) {
            setStateSelected(null);
            setStateList((state: IStock[]) => {
              const udpdateList = stateListStatic.filter((product_) => product_.id !== product.id);
              setStateListStatic(udpdateList);
              return udpdateList;
            });
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;

          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
    }
  };

  return (
    <div>
      <div>
        <Typography.Title level={5} mark>
          &nbsp;{product.name}&nbsp;
        </Typography.Title>
      </div>
      <div>
        <Typography.Text strong>SKU:</Typography.Text> {product.sku}
      </div>
      <div>
        <Form {...LABEL_COL_AND_WRAPPER_COL} form={formUpdate} onFinish={onFinishUpdate}>
          <Form.Item
            label={<strong>Stock</strong>}
            name="stock"
            rules={[
              { required: true, message: "Coloca la cantidad" },
              { type: "number", message: "Escribe un número" },
            ]}
          >
            <InputNumber style={{ width: 150 }} min={1} max={10000000} maxLength={8} />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              Actualizar
            </Button>
          </Form.Item>
        </Form>
      </div>
      <br />
      <br />
      <Row>
        <Col>
          <Form onFinish={onFinishDelete} form={formDelete}>
            <Alert
              message={<span>Eliminar del inventario</span>}
              description={
                <span>
                  Se eliminaran por completo todos los datos del producto <i>{product.name}</i>.
                </span>
              }
              type="error"
              action={
                <Popconfirm
                  title={<span>¿Estás seguro?</span>}
                  icon={<QuestionCircleOutlined style={{ color: "red" }} />}
                  okText="Si"
                  cancelText="No"
                  onConfirm={() => {
                    formDelete.submit();
                  }}
                >
                  <Button size="small" danger>
                    Eliminar
                  </Button>
                </Popconfirm>
              }
            />
            <Form.Item name="id">
              <Input name="id" type="hidden" />
            </Form.Item>
          </Form>
        </Col>
      </Row>
    </div>
  );
};

export default ManageProduct;
