import { useEffect, useState } from "react";
import { Calendar, Button, Modal, Tabs, Space } from "antd";
import jwt_decode from "jwt-decode";
import moment, { Moment } from "moment";
import "moment/locale/es";
import locale from "antd/es/date-picker/locale/es_ES";
import { ConsultationsType } from "../../../shared/enum/consultations-type.enum";
import consultationsServices from "../../../shared/services/consultations.services";
import { ConsultationsStatus } from "../../../shared/enum/consultations-status.enum";
import { errors_translate_es } from "../../../shared/errors/es";
import { IConsultation } from "../../../shared/interfaces/consultations.interfaces";
import FormCreateConsultation from "../../../shared/components/Consultation/FormCreateConsultation";
import { IModal } from "../../../shared/interfaces/modals.interfaces";
import ItemConsultationPending from "./ItemConsultationPending";
import ItemConsultationCancelled from "./ItemConsultationCancelled";
import ItemConsultationAttended from "./ItemConsultationAttended";
import ModalCancel from "./ModalCancel";
import ModalReschedule from "./ModalReschedule";
import "./../../../shared/styles/calendar.css";

const CalendarAll = () => {
  const [modalCrearEvento, setModalCrearEvento] = useState<IModal>({
    visible: false,
  });
  const [stateListConsultations, setStateListConsultations] = useState<IConsultation[]>([]);
  const [stateSelected, setStateSelected] = useState<IConsultation>();
  const [modalReschedule, setModalReschedule] = useState<IModal>({ visible: false });
  const [modalCancel, setModalCancel] = useState<IModal>({ visible: false });

  useEffect(() => {
    const tokenDecode: { id: string } = jwt_decode(window.sessionStorage.getItem("token") || "");

    consultationsServices
      .getConsultationsDoctor(tokenDecode.id)
      .then((response: any) => {
        const { statusCode, data } = response;
        if (statusCode === 200) {
          setStateListConsultations(data);
        }
      })
      .catch((errors) => {
        const { error, message, statusCode } = errors;
        if (statusCode === 409) {
          Modal.warning({
            title: "Hola",
            content: errors_translate_es[message],
          });
        } else {
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        }
      });
  }, []);

  function getListData(date: Moment) {
    let listData: JSX.Element[] = [];

    stateListConsultations?.forEach((consultation) => {
      const fecha = consultation.date.toString().slice(0, 10);
      if (moment(date).format().includes(fecha)) {
        if (consultation.status === ConsultationsStatus.PENDING) {
          listData.push(
            <ItemConsultationPending
              key={consultation.id}
              consultation={consultation}
              setModalReschedule={setModalReschedule}
              setModalCancel={setModalCancel}
              setStateSelected={setStateSelected}
            />
          );
        } else if (
          consultation.status === ConsultationsStatus.CANCELLED_ADMIN ||
          consultation.status === ConsultationsStatus.CANCELLED_DOCTOR ||
          consultation.status === ConsultationsStatus.CANCELLED_PATIENT
        ) {
          listData.push(<ItemConsultationCancelled key={consultation.id} consultation={consultation} />);
        } else if (consultation.status === ConsultationsStatus.ATTENDED) {
          listData.push(<ItemConsultationAttended key={consultation.id} consultation={consultation} />);
        }
      }
    });

    return listData || [];
  }
  function dateCellRender(date: Moment) {
    const listData = getListData(date);
    return <>{listData.map((component) => ({ ...component }))}</>;
  }
  // console.log(`listData`, dateCellRender());

  function getMonthData(data: Moment) {
    if (data.month() === 8) {
      return 1394;
    } else if (data.month() === 4) {
      return 483;
    } else if (data.month() === 1) {
      return 854;
    }
  }

  function monthCellRender(data: Moment) {
    const num = getMonthData(data);
    return num ? (
      <div className="notes-month">
        <section>{num}</section>
        <span>Consultas</span>
      </div>
    ) : null;
  }

  const onPanelChange = (e: any) => {
    // cuando cambia algo en el header, como el mes, años.
  };

  return (
    <>
      <Space style={{ display: "flex", justifyContent: "space-between" }}>
        <Button type="primary" onClick={() => setModalCrearEvento({ ...modalCrearEvento, visible: true })}>
          Agregar evento
        </Button>
        <Modal
          title="Agregar evento"
          visible={modalCrearEvento.visible}
          onCancel={() => setModalCrearEvento({ ...modalCrearEvento, visible: false })}
          footer={false}
        >
          <Tabs defaultActiveKey="1">
            <Tabs.TabPane tab={<span>Consulta</span>} key="1">
              <FormCreateConsultation
                type={ConsultationsType.FACE_TO_FACE}
                setModalCrearEvento={setModalCrearEvento}
                setStateListConsultations={setStateListConsultations}
              />
            </Tabs.TabPane>
            <Tabs.TabPane tab={<span>Cirugía</span>} key="2">
              <FormCreateConsultation
                type={ConsultationsType.SURGERY}
                setModalCrearEvento={setModalCrearEvento}
                setStateListConsultations={setStateListConsultations}
              />
            </Tabs.TabPane>
          </Tabs>
        </Modal>
      </Space>
      <div style={{ height: 10 }}></div>
      <Calendar
        dateCellRender={dateCellRender}
        monthCellRender={monthCellRender}
        locale={locale}
        onPanelChange={onPanelChange}
      />

      {stateSelected && (
        <ModalReschedule
          consultation={stateSelected}
          modalReschedule={modalReschedule}
          setModalReschedule={setModalReschedule}
          setStateListConsultations={setStateListConsultations}
          setStateSelected={setStateSelected}
        />
      )}
      {stateSelected && (
        <ModalCancel
          consultation={stateSelected}
          modalCancel={modalCancel}
          setModalCancel={setModalCancel}
          setStateListConsultations={setStateListConsultations}
          setStateSelected={setStateSelected}
        />
      )}
    </>
  );
};

export default CalendarAll;
