import requestApiConsultations from "../../config/requestApiConsultations";
import { RoleType } from "../enum/roletype.enum";
import {
  IConsultationCancel,
  IConsultationReschedule,
  IConsultationUpdate,
  ICreateConsultation,
} from "../interfaces/consultations.interfaces";

interface DoctorServices {
  create(consultation: ICreateConsultation): Promise<any>;
  getConsultationsDoctor(id: string): Promise<any>;
  updateConsultation(id: string, consultationUpdate: IConsultationUpdate): Promise<any>;
  cancelConsultation(id: string, observations: IConsultationCancel, role: RoleType): Promise<any>;
  rescheduleConsultation(id: string, observations: IConsultationReschedule): Promise<any>;
  getConsultationsPatient(id: string): Promise<any>;
}

const consultationsServices: DoctorServices = {
  create: function (consultation) {
    return requestApiConsultations({
      method: "POST",
      url: "/consultations",
      data: consultation,
      private: true,
    });
  },
  getConsultationsDoctor: function (id) {
    return requestApiConsultations({
      method: "GET",
      url: `/consultations/doctor/${id}`,
      private: true,
    });
  },
  updateConsultation: function (id, consultationUpdate) {
    return requestApiConsultations({
      method: "PUT",
      url: `/consultations/${id}`,
      data: consultationUpdate,
      private: true,
    });
  },
  cancelConsultation: function (id, observations, role: RoleType) {
    if (role === RoleType.PATIENT) {
      return requestApiConsultations({
        method: "PUT",
        url: `/consultations/${id}/cancel-patient`,
        data: observations,
        private: true,
      });
    }
    return requestApiConsultations({
      method: "PUT",
      url: `/consultations/${id}/cancel-doctor`,
      data: observations,
      private: true,
    });
  },
  rescheduleConsultation: function (id, observations) {
    return requestApiConsultations({
      method: "PUT",
      url: `consultations/${id}/reschedule-doctor`,
      data: observations,
      private: true,
    });
  },
  getConsultationsPatient: function (id) {
    return requestApiConsultations({
      method: "GET",
      url: `/consultations/patient/${id}`,
      private: true,
    });
  },
};

export default consultationsServices;
