import { Button } from "antd";
import { SizeType } from "antd/lib/config-provider/SizeContext";
import { useHistory } from "react-router-dom";
import { IConsultation } from "../../interfaces/consultations.interfaces";

const ButtonAttend = (props: { size: SizeType, consultation: IConsultation }) => {
  const { size, consultation } = props;
  const history = useHistory();
  const goToAtted = () => {
    history.push({
      pathname: "/doctor/attending",
      state: { consultation },
    });
  };

  return (
    <Button type="primary" ghost size={size} onClick={goToAtted}>
      Atender
    </Button>
  );
};

export default ButtonAttend;
