import requestApiConsultations from "../../config/requestApiConsultations";
import { ICreateMedicalHistory } from "../interfaces/medical-history.interfaces";

interface MedicalHistoryServices {
  create(medicalHistory: ICreateMedicalHistory): Promise<any>;
}

const medicalHistoryServices: MedicalHistoryServices = {
  create: function (medicalHistory) {
    return requestApiConsultations({
      method: "POST",
      url: "/medical-history",
      data: medicalHistory,
      private: true,
    });
  },
};

export default medicalHistoryServices;
