import { Layout, Menu, Popover, Button } from "antd";
import { Switch, Route, Link, useHistory } from "react-router-dom";
import { UserOutlined } from "@ant-design/icons";
import Doctores from "../Doctores";
import ButtonSignOut from "../../../shared/components/ButtonSignOut";
import { Auth } from "../../../shared/utils/auth";
import { RoleType } from "../../../shared/enum/roletype.enum";
import ResultForbidden from "../../../shared/components/Results/ResultForbidden";
import ResultNotFound from "../../../shared/components/Results/ResultNotFound";
const { Header, Content, Sider } = Layout;

const AppAdmin = () => {
  const history = useHistory();
  const tokenDecode = Auth.tokenDecode();
  if (!tokenDecode.validToken) {
    history.push("/auth/login");
    return <></>;
  }
  if (tokenDecode.role !== RoleType.ADMIN) return <ResultForbidden />;

  const isResponsive = window.matchMedia("screen and (max-width: 600px)").matches;

  const HeaderApp = (): JSX.Element => {
    return (
      <Header
        className="header"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          padding: "0 15px",
          backgroundColor: "#EFF2F5",
        }}
      >
        <div className="logo">
          <Link to="/doctor">
            <img
              src="https://www.tuhospitalvirtual.com/themes/tuagendamedica/resources/images/logos/logoweb.png"
              height="50"
              alt="THV"
            />
          </Link>
        </div>

        <Popover
          placement="bottomRight"
          title={
            <span>
              {tokenDecode.firstName} {tokenDecode.lastName} <br /> {tokenDecode.email}
            </span>
          }
          content={
            <div>
              {/* <p>Mi Cuenta</p> */}
              <p>
                <ButtonSignOut />
              </p>
            </div>
          }
          trigger="click"
        >
          <Button
            style={{
              borderRadius: "100px",
              height: "50px",
              width: "50px",
            }}
          >
            {tokenDecode.firstName?.charAt(0)}
            {tokenDecode.lastName?.charAt(0)}
          </Button>
        </Popover>
      </Header>
    );
  };

  const ContentApp = (): JSX.Element => {
    return (
      <Content
        className="site-layout-background"
        style={{
          padding: 10,
          margin: 0,
          minHeight: 280,
          backgroundColor: "#FFFFFF",
        }}
      >
        <Switch>
          <Route exact path="/admin/doctores" component={Doctores} />
          <Route exact path="/admin" component={Doctores} />
          <Route path="/admin" component={ResultNotFound} />
        </Switch>
      </Content>
    );
  };

  if (isResponsive) {
    return (
      <Layout style={{ height: "100vh" }}>
        <HeaderApp />
        <ContentApp />
        <Layout.Footer style={{ padding: 0, zIndex: 10 }}>
          <div style={{ display: "flex" }}>
            <Link to="/admin/doctores" style={{ width: "100%" }}>
              <Button size="small" style={{ height: 50, width: "100%" }}>
                <UserOutlined />
                <br />
                Doctores
              </Button>
            </Link>
          </div>
        </Layout.Footer>
      </Layout>
    );
  }

  return (
    <Layout style={{ height: "100vh" }}>
      <HeaderApp />
      <Layout>
        <Sider width={170} className="site-layout-background">
          <Menu
            mode="inline"
            defaultSelectedKeys={["1"]}
            defaultOpenKeys={["sub1"]}
            style={{ height: "100%", borderRight: 0 }}
          >
            <Menu.Item key="calendario" icon={<UserOutlined />}>
              <Link to="/admin/doctores">Doctores</Link>
            </Menu.Item>
          </Menu>
        </Sider>

        <ContentApp />
      </Layout>
    </Layout>
  );
};

export default AppAdmin;
