import { Layout, Menu, Popover, Button } from "antd";
import { Switch, Route, Link, useHistory } from "react-router-dom";
import { UsergroupAddOutlined, PicCenterOutlined, CalendarOutlined, AimOutlined } from "@ant-design/icons";
import Patients from "../Patients";
import Calendario from "../Calendar/CalendarAll";
import Attending from "../Consultations/Attending";
import Horarios from "../Horarios";
import ButtonSignOut from "../../../shared/components/ButtonSignOut";
import { Auth } from "../../../shared/utils/auth";
import { RoleType } from "../../../shared/enum/roletype.enum";
import ResultForbidden from "../../../shared/components/Results/ResultForbidden";
import Inventory from "../Inventory";
import CalendarToday from "../Calendar/CalendarToday";
import Attended from "../../../shared/components/ConsultationAttended";
import ResultNotFound from "../../../shared/components/Results/ResultNotFound";

const AppDoctor = (): JSX.Element => {
  const history = useHistory();
  const tokenDecode = Auth.tokenDecode();
  if (!tokenDecode.validToken) {
    history.push("/auth/login");
    return <></>;
  }
  if (tokenDecode.role !== RoleType.DOCTOR) return <ResultForbidden />;

  const isResponsive = window.matchMedia("screen and (max-width: 600px)").matches;

  const HeaderApp = (): JSX.Element => {
    return (
      <Layout.Header
        className="header"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          padding: "0 15px",
          backgroundColor: "#EFF2F5",
        }}
      >
        <div className="logo">
          <Link to="/doctor">
            <img
              src="https://www.tuhospitalvirtual.com/themes/tuagendamedica/resources/images/logos/logoweb.png"
              height="50"
              alt="THV"
            />
          </Link>
        </div>

        <Popover
          placement="bottomRight"
          title={
            <span>
              {tokenDecode.firstName} {tokenDecode.lastName} <br /> {tokenDecode.email}
            </span>
          }
          content={
            <div>
              <p>
                <Button type="primary" ghost onClick={() => history.push("/doctor/horarios")}>
                  Horarios
                </Button>
              </p>
              <p>
                <ButtonSignOut />
              </p>
            </div>
          }
          trigger="click"
        >
          <Button
            style={{
              borderRadius: "100px",
              height: "50px",
              width: "50px",
            }}
          >
            {tokenDecode.firstName?.charAt(0)}
            {tokenDecode.lastName?.charAt(0)}
          </Button>
        </Popover>
      </Layout.Header>
    );
  };

  const ContentApp = (): JSX.Element => {
    return (
      <Layout.Content
        className="site-layout-background"
        style={{
          padding: 10,
          margin: 0,
          minHeight: 280,
          backgroundColor: "#FFFFFF",
        }}
      >
        <Switch>
          <Route exact path="/doctor/calendario" component={Calendario} />
          <Route exact path="/doctor/calendar/today" component={CalendarToday} />
          <Route exact path="/doctor/horarios" component={Horarios} />
          <Route exact path="/doctor/pacientes" component={Patients} />
          <Route exact path="/doctor/attending" component={Attending} />
          <Route exact path="/doctor/attended" component={Attended} />
          <Route exact path="/doctor/inventario" component={Inventory} />
          <Route exact path="/doctor" component={Calendario} />
          <Route path="/doctor" component={ResultNotFound} />
        </Switch>
      </Layout.Content>
    );
  };

  if (isResponsive) {
    return (
      <Layout style={{ height: "100vh" }}>
        <HeaderApp />
        <ContentApp />
        <Layout.Footer style={{ padding: 0, zIndex: 10 }}>
          <div style={{ display: "flex" }}>
            <Link to="/doctor/calendar/today" style={{ width: "100%" }}>
              <Button size="small" style={{ height: 50, width: "100%" }}>
                <AimOutlined />
                <br />
                Hoy
              </Button>
            </Link>
            <Link to="/doctor/calendario" style={{ width: "100%" }}>
              <Button size="small" style={{ height: 50, width: "100%" }}>
                <CalendarOutlined />
                <br />
                Calendario
              </Button>
            </Link>
            <Link to="/doctor/pacientes" style={{ width: "100%" }}>
              <Button size="small" style={{ height: 50, width: "100%" }}>
                <UsergroupAddOutlined />
                <br />
                Pacientes
              </Button>
            </Link>
            <Link to="/doctor/inventario" style={{ width: "100%" }}>
              <Button size="small" style={{ height: 50, width: "100%" }}>
                <PicCenterOutlined />
                <br />
                Inventario
              </Button>
            </Link>
          </div>
        </Layout.Footer>
      </Layout>
    );
  }

  return (
    <Layout style={{ height: "100vh" }}>
      <HeaderApp />
      <Layout>
        <Layout.Sider width={170} className="site-layout-background">
          <Menu
            mode="inline"
            defaultSelectedKeys={["1"]}
            defaultOpenKeys={["sub1"]}
            style={{ height: "100%", borderRight: 0 }}
          >
            <Menu.Item key="today" icon={<AimOutlined />}>
              <Link to="/doctor/calendar/today">Hoy</Link>
            </Menu.Item>
            <Menu.Item key="calendario" icon={<CalendarOutlined />}>
              <Link to="/doctor/calendario">Calendario</Link>
            </Menu.Item>
            <Menu.Item key="pacientes" icon={<UsergroupAddOutlined />}>
              <Link to="/doctor/pacientes">Pacientes</Link>
            </Menu.Item>
            <Menu.Item key="inventario" icon={<PicCenterOutlined />}>
              <Link to="/doctor/inventario">Inventario</Link>
            </Menu.Item>
            {/* <Menu.Item key="notificaciones" icon={<NotificationOutlined />}>
              <Link to="/notificaciones">Notificaciones</Link>
            </Menu.Item> */}
          </Menu>
        </Layout.Sider>

        <ContentApp />
      </Layout>
    </Layout>
  );
};

export default AppDoctor;
