import { useEffect, useState } from "react";
import { Button, Modal, Tabs } from "antd";
import { Auth } from "../../../shared/utils/auth";
import { TypeResponse, TypeResponseError } from "../../../shared/types/response.type";
import consultationsServices from "../../../shared/services/consultations.services";
import { IConsultation } from "../../../shared/interfaces/consultations.interfaces";
import ListConsultation from "../../../shared/components/ConsultationList";
import "./../../../shared/styles/calendar.css";
import FormCreateConsultation from "../../../shared/components/Consultation/FormCreateConsultation";
import { ConsultationsType } from "../../../shared/enum/consultations-type.enum";
import { IModal } from "../../../shared/interfaces/modals.interfaces";

let stateListStatic: IConsultation[] = [];

const CalendarList = () => {
  const tokenDecode = Auth.tokenDecode();

  const [modalCrearEvento, setModalCrearEvento] = useState<IModal>({
    visible: false,
  });
  const [stateList, setStateList] = useState<IConsultation[]>([]);
  const setStateListStatic = (list: IConsultation[]): void => {
    stateListStatic = list;
  };

  useEffect(() => {
    if (tokenDecode.id) {
      consultationsServices
        .getConsultationsPatient(tokenDecode.id)
        .then((response: TypeResponse) => {
          const { statusCode, data } = response;
          if (statusCode === 200) {
            setStateList(data);
            setStateListStatic(data);
          }
        })
        .catch((errors: TypeResponseError) => {
          const { error, message, statusCode } = errors;
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        });
    }
  }, [tokenDecode.id]);

  return (
    <>
      <Button type="primary" onClick={() => setModalCrearEvento({ ...modalCrearEvento, visible: true })}>
        Nueva consulta
      </Button>
      <Modal
        title="Agregar evento"
        visible={modalCrearEvento.visible}
        onCancel={() => setModalCrearEvento({ ...modalCrearEvento, visible: false })}
        footer={false}
      >
        <Tabs defaultActiveKey="1">
          <Tabs.TabPane tab={<span>Consulta</span>} key="1">
            <FormCreateConsultation
              type={ConsultationsType.FACE_TO_FACE}
              setModalCrearEvento={setModalCrearEvento}
              setStateListConsultations={setStateList}
            />
          </Tabs.TabPane>
        </Tabs>
      </Modal>
      <br />
      <br />
      <ListConsultation consultations={stateList} setStateList={setStateList} stateListStatic={stateListStatic} />
    </>
  );
};

export default CalendarList;
