import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import AppDoctor from "./apps/doctor/AppDoctor";
import Auth from "./apps/Auth";
import AppAdmin from "./apps/admin/AppAdmin";
import ResultNotFound from "./shared/components/Results/ResultNotFound";
import AppPatient from "./apps/patient/AppPatient";

const App = () => (
  <Router>
    <Switch>
      <Route path="/auth" component={Auth} />
      <Route path="/admin" component={AppAdmin} />
      <Route path="/doctor" component={AppDoctor} />
      <Route path="/patient" component={AppPatient} />
      <Route path="/" component={ResultNotFound} />
    </Switch>
  </Router>
);

export default App;
