import requestApiConsultations from "../../config/requestApiConsultations";
import { ICreateDoctor, IDoctorDetail, IScheduleDay } from "../interfaces/doctor.interfaces";
import { IUser } from "../interfaces/user.interfaces";

interface DoctorServices {
  create(doctor: ICreateDoctor): Promise<any>;
  getSchedule(doctorId: string): Promise<any>;
  updateSchedule(doctorId: string, schedule: IScheduleDay): Promise<any>;
  getDoctors(): Promise<any>;
  updateDoctor(id: string, userAndDoctorDetails: { user: IUser; doctorDetail: IDoctorDetail }): Promise<any>;
}

const doctorServices: DoctorServices = {
  create: function (doctor) {
    return requestApiConsultations({
      method: "POST",
      url: "/doctors",
      data: doctor,
      private: true,
    });
  },
  getSchedule: function (doctorId) {
    return requestApiConsultations({
      method: "GET",
      url: `/doctors/${doctorId}/schedule`,
      private: true,
    });
  },
  updateSchedule: function (doctorId, schedule) {
    return requestApiConsultations({
      method: "PUT",
      url: `/doctors/${doctorId}/schedule`,
      data: schedule,
      private: true,
    });
  },
  getDoctors: function () {
    return requestApiConsultations({
      method: "GET",
      url: "/doctors",
      private: true,
    });
  },
  updateDoctor: function (id: string, userAndDoctorDetails: { user: IUser; doctorDetail: IDoctorDetail }) {
    return requestApiConsultations({
      method: "PUT",
      url: `/doctors/${id}`,
      data: userAndDoctorDetails,
      private: true,
    });
  },
};

export default doctorServices;
