import requestApiConsultations from "../../config/requestApiConsultations";
import { IProductUpdate, IStock } from "../interfaces/inventory.interfaces";

interface InventoryServices {
  addInventory(stock: IStock): Promise<any>;
  getInventory(doctorId: string): Promise<any>;
  updateInventory(id: string, product: IProductUpdate): Promise<any>;
  deleteInventory(id: string): Promise<any>;
}

const inventoryServices: InventoryServices = {
  addInventory: function (stock) {
    return requestApiConsultations({
      method: "POST",
      url: "/inventory",
      data: stock,
      private: true,
    });
  },
  getInventory: function (doctorId: string) {
    return requestApiConsultations({
      method: "GET",
      url: `/inventory/doctor/${doctorId}`,
      private: true,
    });
  },
  updateInventory: function (id: string, product: IProductUpdate) {
    return requestApiConsultations({
      method: "PUT",
      url: `/inventory/${id}/stock`,
      data: product,
      private: true,
    });
  },
  deleteInventory: function (id: string) {
    return requestApiConsultations({
      method: "DELETE",
      url: `/inventory/${id}`,
      private: true,
    });
  },
};

export default inventoryServices;
