import { Image, Space, Modal } from "antd";
import { useEffect, useState } from "react";
import { errors_translate_es } from "../../errors/es";
import { IConsultation } from "../../interfaces/consultations.interfaces";
import filesServices from "../../services/files.services";

const ConsultationAttachedFiles = (props: { consultation: IConsultation; isAttending: boolean }) => {
  const { consultation } = props;

  const [images, setImages] = useState<string[]>([]);
  const [pdfs, setPdfs] = useState<string[]>([]);

  useEffect(() => {
    filesServices
      .getAttachedFilesByConsultationId(consultation.id)
      .then((response: { statusCode: number; data: { images: string[]; pdfs: string[] } }) => {
        const { statusCode, data } = response;
        if (statusCode === 200) {
          if (data.images && data.images.length > 0) {
            setImages(data.images);
          }
          if (data.pdfs && data.pdfs.length > 0) {
            setPdfs(data.pdfs);
          }
        }
      })
      .catch((errors) => {
        const { error, message, statusCode } = errors;
        if (statusCode === 409) {
          Modal.warning({
            title: "Hola",
            content: errors_translate_es[message],
          });
        } else {
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        }
      });
  }, [consultation.id]);

  return (
    <>
      <strong>Adjuntos:</strong>
      <div style={{ height: 5 }}></div>
      <Space direction="vertical">
        <Space direction="vertical">
          {pdfs.map((pdf) => (
            <div key={pdf}>
              <a target="_blank" rel="noreferrer" href={`${process.env.REACT_APP_FILES_URL}/${pdf}`}>
                {pdf}
              </a>
            </div>
          ))}
        </Space>
        <Space direction="horizontal">
          {images.map((image) => (
            <Image key={image} width={60} height={60} src={`${process.env.REACT_APP_FILES_URL}/${image}`} />
          ))}
        </Space>
      </Space>
    </>
  );
};

export default ConsultationAttachedFiles;
