import { useState } from "react";
import { useLocation, useHistory } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
import { Button, Col, Row, Select, Tabs, Form, Input, Space, Modal, Divider, InputNumber, Typography } from "antd";
import { FileDoneOutlined } from "@ant-design/icons";
import MedicalHistory from "../../../shared/components/MedicalHistory";
import ConsultationInfo from "../../../shared/components/ConsultationInfo";
import { cie10 } from "../../../shared/data/cie10";
import consultationsServices from "../../../shared/services/consultations.services";
import {
  IConsultation,
  IConsultationUpdate,
  TypeEvaluation,
  TypeExam,
  TypePrescription,
  TypeQuote,
} from "../../../shared/interfaces/consultations.interfaces";
import { errors_translate_es } from "../../../shared/errors/es";
import ConsultationAttachedFiles from "../../../shared/components/ConsultationAttachedFiles";
import { getConsultationsTypeTitle } from "../../../shared/utils/consultations";
import Preview from "./Preview";

const Attending = () => {
  const history = useHistory();
  const location = useLocation();
  const { state } = location;
  const state_: any | IConsultation = state;

  const [formPrescription] = Form.useForm();
  const [formExam] = Form.useForm();
  const [formQuote] = Form.useForm();

  const [stateEvaluation, setStateEvaluation] = useState<TypeEvaluation>({
    cie10: "",
    observations: "",
  });
  const [statePrescription, setStatePrescription] = useState<TypePrescription[]>([]);
  const [stateExams, setStateExams] = useState<TypeExam[]>([]);
  const [stateQuote, setStateQuote] = useState<TypeQuote[]>([]);

  const updateConsultation = (): void => {
    const consultation: IConsultationUpdate = {
      observations: [],
      prescriptions: [],
      exams: [],
      quote: [],
    };

    const observations_: string = stateEvaluation.observations.trim();

    if (observations_.length < 1) {
      Modal.warning({
        title: "Hola",
        content: "Escribe las observaciones de la evaluación",
      });
      return;
    }

    if (stateEvaluation.cie10.length > 0) {
      consultation.observations.push(stateEvaluation.cie10);
    }

    consultation.observations.push(stateEvaluation.observations);

    if (statePrescription.length > 0) {
      statePrescription.forEach((prescription) => {
        if (prescription.value) {
          consultation.prescriptions.push(prescription.value);
        }
      });
    }

    if (stateExams.length > 0) {
      stateExams.forEach((exams) => {
        if (exams.value) {
          consultation.exams.push(exams.value);
        }
      });
    }

    if (stateQuote.length > 0) {
      stateQuote.forEach((quote) => {
        if (quote.service && quote.cost) {
          consultation.quote.push({
            service: quote.service,
            cost: quote.cost,
          });
        }
      });
    }

    consultationsServices
      .updateConsultation(state_.consultation.id, consultation)
      .then((response: any) => {
        const { statusCode } = response;
        if (statusCode === 200) {
          Modal.info({
            title: "Hola",
            content: "Consulta atendida",
          });
          history.push("/doctor/calendario");
        }
      })
      .catch((errors) => {
        const { error, message, statusCode } = errors;
        if (statusCode === 409) {
          Modal.warning({
            title: "Hola",
            content: errors_translate_es[message],
          });
        } else {
          Modal.error({
            title: "Hola",
            content: `${message} (${statusCode} ${error})`,
          });
        }
      });
  };

  const onChangeCIE10 = (value: any) => {
    setStateEvaluation({
      ...stateEvaluation,
      cie10: value,
    });
  };

  const onChangeObservations = (e: any) => {
    setStateEvaluation({
      ...stateEvaluation,
      observations: e.target.value,
    });
  };

  const onFinishPrescription = (values: { prescription: string }) => {
    setStatePrescription([
      ...statePrescription,
      {
        id: uuidv4(),
        value: values.prescription,
      },
    ]);
    formPrescription.resetFields();
  };

  const handleDeletePrescription = (id: string) => {
    setStatePrescription(statePrescription.filter((prescription) => prescription.id !== id));
  };

  const onFinishExam = (values: { exam: string }) => {
    setStateExams([
      ...stateExams,
      {
        id: uuidv4(),
        value: values.exam,
      },
    ]);
    formExam.resetFields();
  };

  const handleDeleteExam = (id: string) => {
    setStateExams(stateExams.filter((exam) => exam.id !== id));
  };

  const onFinishQuote = (values: TypeQuote) => {
    setStateQuote([
      ...stateQuote,
      {
        id: uuidv4(),
        service: values.service,
        cost: values.cost,
      },
    ]);
    formQuote.resetFields();
  };

  const handleDeleteQuote = (id: string) => {
    setStateQuote(stateQuote.filter((quote) => quote.id !== id));
  };

  return (
    <>
      <Row>
        <Col lg={24}>
          <Tabs defaultActiveKey="1">
            <Tabs.TabPane tab={<span>{getConsultationsTypeTitle(state_.consultation.type)}</span>} key="2">
              <Row gutter={[10, 10]}>
                <Col lg={11}>
                  <ConsultationInfo consultation={state_.consultation} />
                  <Divider></Divider>
                  <ConsultationAttachedFiles consultation={state_.consultation} isAttending />
                </Col>
                <Col
                  lg={13}
                  style={{
                    backgroundColor: "#FFFFFF",
                    height: "83vh",
                    overflow: "scroll",
                  }}
                >
                  <MedicalHistory patientSelected={state_.consultation.patient} />
                </Col>
              </Row>
            </Tabs.TabPane>
            <Tabs.TabPane tab={<Typography.Text>Evaluación</Typography.Text>} key="3">
              <Row>
                <Col lg={15}>
                  <Form.Item
                    labelCol={{ span: 24 }}
                    wrapperCol={{ span: 24 }}
                    name="cie10"
                    label={<strong>Diagnóstico (CIE 10)</strong>}
                  >
                    <Select
                      showSearch
                      placeholder="Busca una enfermedad"
                      optionFilterProp="children"
                      onChange={onChangeCIE10}
                      // onFocus={onFocus}
                      // onBlur={onBlur}
                      // onSearch={onSearch}
                      // filterOption={(input, option) => option?.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                    >
                      {cie10.map((disease, index) => {
                        return (
                          <Select.Option key={index} value={disease.name}>
                            {disease.name}
                          </Select.Option>
                        );
                      })}
                    </Select>
                  </Form.Item>
                  <Form.Item
                    labelCol={{ span: 24 }}
                    wrapperCol={{ span: 24 }}
                    name="observations"
                    label={<strong>Observaciones</strong>}
                    rules={[
                      { required: true, message: "Escribe las observaciones de la consulta detalladamente" },
                      { min: 5, message: "Minimo 5 caracteres" },
                      { max: 1000, message: "Máximo 1000 caracteres" },
                    ]}
                  >
                    <Input.TextArea rows={12} onChange={onChangeObservations} autoFocus maxLength={1001} />
                  </Form.Item>
                </Col>
              </Row>
            </Tabs.TabPane>
            <Tabs.TabPane
              tab={<span>Recetas {statePrescription.length > 0 && `(${statePrescription.length})`}</span>}
              key="4"
            >
              {statePrescription?.map((prescription) => (
                <>
                  <div>{prescription.value}</div>
                  <div>
                    <Space>
                      {/* <Button type="primary" ghost>
                        Actualizar
                      </Button> */}
                      <Button
                        type="ghost"
                        danger
                        onClick={() => {
                          handleDeletePrescription(prescription.id || "NoId");
                        }}
                      >
                        Eliminar
                      </Button>
                    </Space>
                  </div>
                  <br />
                </>
              ))}
              {statePrescription.length > 0 && (
                <>
                  <br />
                </>
              )}
              {statePrescription.length < 5 && (
                <Form onFinish={onFinishPrescription} form={formPrescription}>
                  <Row>
                    <Col lg={12}>
                      <Form.Item
                        labelCol={{ span: 24 }}
                        wrapperCol={{ span: 24 }}
                        name="prescription"
                        label={<strong>Receta</strong>}
                        rules={[{ required: true, message: "Escribe la receta" }]}
                      >
                        <Input autoFocus />
                      </Form.Item>
                    </Col>
                  </Row>
                  <br />
                  <Button type="primary" ghost htmlType="submit">
                    Agregar receta
                  </Button>
                </Form>
              )}
            </Tabs.TabPane>
            <Tabs.TabPane tab={<span>Exámenes {stateExams.length > 0 && `(${stateExams.length})`}</span>} key="5">
              {stateExams?.map((exam) => (
                <>
                  <div>{exam.value}</div>
                  <div>
                    <Space>
                      {/* <Button type="primary" ghost>
                        Actualizar
                      </Button> */}
                      <Button
                        type="ghost"
                        danger
                        onClick={() => {
                          handleDeleteExam(exam.id || "NoId");
                        }}
                      >
                        Eliminar
                      </Button>
                    </Space>
                  </div>
                  <br />
                </>
              ))}
              {stateExams.length > 0 && (
                <>
                  <br />
                </>
              )}

              {stateExams.length < 5 && (
                <Form onFinish={onFinishExam} form={formExam}>
                  <Row>
                    <Col lg={12}>
                      <Form.Item
                        labelCol={{ span: 24 }}
                        wrapperCol={{ span: 24 }}
                        name="exam"
                        label={<strong>Exámen</strong>}
                        rules={[{ required: true, message: "Escribe el exámen" }]}
                      >
                        <Input autoFocus />
                      </Form.Item>
                    </Col>
                  </Row>
                  <br />
                  <Button type="primary" ghost htmlType="submit">
                    Agregar exámen
                  </Button>
                </Form>
              )}
            </Tabs.TabPane>
            <Tabs.TabPane tab={<span>Cotización {stateQuote.length > 0 && `(${stateQuote.length})`}</span>} key="quote">
              {stateQuote?.map((quote) => (
                <>
                  <div>
                    <strong>Servicio:</strong> {quote.service}
                  </div>
                  <div>
                    <strong>Costo:</strong> {quote.cost}
                  </div>
                  <div>
                    <Space>
                      <Button
                        type="ghost"
                        danger
                        onClick={() => {
                          handleDeleteQuote(quote.id || "NoId");
                        }}
                      >
                        Eliminar
                      </Button>
                    </Space>
                  </div>
                  <br />
                </>
              ))}
              {stateQuote.length > 0 && (
                <>
                  <br />
                </>
              )}

              {stateQuote.length < 5 && (
                <Form onFinish={onFinishQuote} form={formQuote}>
                  <Row gutter={[10, 10]}>
                    <Col lg={10}>
                      <Form.Item
                        labelCol={{ span: 24 }}
                        wrapperCol={{ span: 24 }}
                        name="service"
                        label={<strong>Servicio</strong>}
                        rules={[{ required: true, message: "Escribe el servicio" }]}
                      >
                        <Input autoFocus />
                      </Form.Item>
                    </Col>
                    <Col lg={4}>
                      <Form.Item
                        labelCol={{ span: 24 }}
                        wrapperCol={{ span: 24 }}
                        name="cost"
                        label={<strong>Costo</strong>}
                        rules={[
                          { required: true, message: "Escribe costo del servicio" },
                          { type: "number", message: "Escribe un número" },
                        ]}
                      >
                        <InputNumber min={1} />
                      </Form.Item>
                    </Col>
                  </Row>
                  <br />
                  <Button type="primary" ghost htmlType="submit">
                    Agregar servicio
                  </Button>
                </Form>
              )}
            </Tabs.TabPane>
            <Tabs.TabPane
              tab={
                <Typography.Text code>
                  <FileDoneOutlined />
                  Vista previa
                </Typography.Text>
              }
              key="preview"
              style={{
                backgroundColor: "#FFFFFF",
                height: "83vh",
                overflow: "scroll",
              }}
            >
              <Preview
                consultation={{
                  ...state_.consultation,
                  evaluation: stateEvaluation,
                  prescriptions: statePrescription.map((prescription) => prescription.value),
                  exams: stateExams.map((exam) => exam.value),
                  quote: stateQuote,
                }}
              />
              <div style={{ textAlign: "left", paddingBottom: 50 }}>
                <Button type="primary" onClick={updateConsultation}>
                  Finalizar Consulta
                </Button>
              </div>
            </Tabs.TabPane>
          </Tabs>
        </Col>
      </Row>
    </>
  );
};

export default Attending;
